import React, { useEffect, useState } from 'react';
import { Button, Modal, Input } from 'antd';
import { ethers } from 'ethers'

import api from "../../api";
import { useWeb3 } from "../../api/contracts";
import { useTranslation } from "react-i18next";
import remove from "../../img/shutdown.png";
import add from "../../img/Add.png";

import styled from 'styled-components';

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`



export default function AddNew(props) {

    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, orgcontract, whoAmI, daoOrg } = state;

    const [tips, setTips] = useState('');

    const [name, setname] = useState('');
    const [address, setaddress] = useState('');

    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    // const [addModerator, setaddModerator] = useState(false);
    // const [addMember, setaddMember] = useState(false);
    const [adminlist, setadminlist] = useState([
        {
            name: '',
            address: ''
        }
    ]);
    const [errorShow, seterrorShow] = useState(false);

    let { t } = useTranslation();

    let { handleClose, showTips, typeName, applyAuth } = props;

    useEffect(()=>{
        if(whoAmI == null) return
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
    },[whoAmI])


    const submitModerators = async (obj) => {
        dispatch({ type: 'LOADINGTYPE', payload: t('addOrg.AddModerator') });
        props.handleClose();

        const signer = web3Instance.getSigner(Account)
        try {
            const res = await api.org.addDaoModerator(daoOrg, signer, obj)
            await res.wait();

            // setaddModerator(result);
            props.refresh();
            setname('');
            setaddress('');
        } catch (error) {
            console.error(error)
            seterrorShow(true);
            const msg = (error && error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('addOrg.AddModerator')}: ${msg}` } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }
    const submitMembers = async (obj) => {
        dispatch({ type: 'LOADINGTYPE', payload: t('addOrg.AddMember') });
        props.handleClose();
        const signer = web3Instance.getSigner(Account)
        
      
        try {
            const res = await api.org.addDaoMember(daoOrg, signer, obj)
            await res.wait();
            // setaddModerator(result);
            props.refresh();
            setname('');
            setaddress('');
        } catch (error) {
            console.error(error)
            if (error && error.data && error.data.message) {
                console.warning(`!${error.data.message}!`)
            }
            seterrorShow(true);
            const msg = (error && error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg:  `${t('addOrg.AddMember')}: ${msg}`} });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        let str = `set${name}`;
        eval(str)(value)
    }
    const handleSubmit = (type) => {
        const obj = {
            address: [address],
            name: [name]
        };

        if (type === "Moderators") {
            submitModerators(obj)
        } else {
            submitMembers(obj)
        }
    }
    const setAdminInput = (e, index) => {
        let newArray = [...adminlist];
        const { name, value } = e.target;
        newArray[index][name] = value;
        setadminlist(newArray)
    }
    const removeAdmin = (selectItem, index) => {
        let newArray = [...adminlist];
        newArray.splice(index, 1);
        setadminlist(newArray)
    }
    const handleBatch = () => {
        props.handleBatch()
    }

    return <div>
        <Modal visible={showTips} onCancel={handleClose} footer={null}>
            <div className="title">
                <span >{typeName==='Members'?`${t('addOrg.AddMember')}`:`${t('addOrg.AddModerator')}`}</span>
            </div>

            <div className="label">{t('addOrg.Name')}</div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('addOrg.Name')}
                    name='name'
                    value={name}
                    autoComplete="off"
                    onChange={handleInputChange}
                />
            </div>

            <div className="label">{t('addOrg.Address')}</div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('addOrg.Address')}
                    name='address'
                    value={address}
                    autoComplete="off"
                    onChange={handleInputChange}
                />
            </div>

            <NextBrdr >
                <Button type="primary" onClick={() => handleSubmit(typeName)} style={{ width: '100%', marginTop: '3rem' }}>
                    {t('addOrg.Submit')}
                </Button>
                {
                    typeName === 'Members' && (isModerator || isOwner) &&
                    <Button className="default" onClick={() => handleBatch()} style={{ width: '100%', marginTop: '3rem' }}>
                        {t('addOrg.Batch')}
                    </Button>
                }
            </NextBrdr>
        </Modal>
    </div>;
}
