import React, { useState, forwardRef, useImperativeHandle } from 'react'
import { DatePicker, Select, Input } from 'antd'
import { useTranslation } from 'react-i18next';
import moment from 'moment';
import { useWeb3 } from '../../../api/contracts';
import Estimate from '../../ventureModal/estimate';
import { SelectShortBrdr } from './styles'
import { ethers } from "ethers"
import TokenSelect from "./tokenSelect"

const ApproveGP = React.memo(forwardRef((props, ref)=>{
    const { gplist, tokenlist } = props

    const { t } = useTranslation();

    const { state } = useWeb3();
    const { vaultcontract, web3Instance } = state;
    
    const [approveDuration, setApproveDuration] = useState(null)
    const [selectGP, setSelectGP] = useState(null)

    const [selectToken, setSelectToken] = useState({})
    const [tokenAmount, setTokenAmount] = useState(0)


    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }
    const handleApproveDuration = v=>{
        const dateTime = Date.parse(v._d)
        setApproveDuration(dateTime/1000)
    }

    const handleInputChangeAmount = async (amount) => {
        setTokenAmount(amount)
    }

    const handleSelect = async(value) => {
        const tk = tokenlist.find(t => t.address === value)
        setSelectToken(tk)
    }

    useImperativeHandle(ref, () => ({
        checkValid() {
            if (!selectGP || !approveDuration) {
                return false 
            }
            if (tokenAmount <= 0) {
                return false
            }
            if (!selectToken.address) {
                return false
            }
            if (Number(selectToken.balanceDisplay) < tokenAmount) {
                return false
            }
            return true
        },
        data: {
            gpAddress: selectGP,
            callee: vaultcontract.address,
            limit: {
                address: selectToken.address,
                amount: tokenAmount ? ethers.utils.parseUnits(tokenAmount, selectToken.deci) : 0
            },
            approvTime:  approveDuration
        }
    }));

    return <>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.Duration')}</div>
            </div>
            <DatePicker
                className="myDatePicker"
                placeholder={t('vote.Duration')}
                style={{ width: '100%' }}
                format="YYYY-MM-DD HH:mm:ss"
                allowClear={false}
                disabledDate={disabledDate}
                onChange={handleApproveDuration}
                showTime={true}
                showNow={false} />
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveGp')}</div>
            </div>
            <SelectShortBrdr onChange={(v)=>{setSelectGP(v)}}>
                {
                    gplist.map((item, i)=>{
                        return (
                            <Select.Option value={item.address} key={i}>
                                <div className='select-item'>{item.address} </div>
                            </Select.Option>
                        )
                    })
                }
            </SelectShortBrdr>
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveLimit')}</div>
            </div>
            <TokenSelect list={tokenlist} handleSelect={handleSelect} t={t} />
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveLimitAmount')}</div>
            </div>
            <div className='inputBrdr'>
                <Input
                    type="number"
                    placeholder={t('vote.enter')}
                    onChange={(e) => handleInputChangeAmount(e.target.value)}
                    autoComplete="off"/>
                <span className='suffix'>{selectToken.symbol}</span>
            </div>
        </div>
    </>
}))

export default ApproveGP