import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Modal, Input, Select } from 'antd';

import {ethers} from 'ethers';
import api from "../../api";

import { useWeb3 } from "../../api/contracts";

import { useTranslation } from "react-i18next";
import publicJs from "../../utils/publicJs";

const Deposit = forwardRef((props, ref) => {
    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, vaultcontract, erc20contract, daoOrg, daoContractAddress, decimals } = state;

    const [selected, setSelected] = useState(null);
    const [amount, setAmount] = useState('');
    const [deposit, setdeposit] = useState(false);
    const [defaultSelectAddress, setDefaultSelectAddress] = useState('')

    const [list, setList] = useState([]);


    let { t } = useTranslation();

    useEffect(() => {
        if (vaultcontract == null || erc20contract == null) return;
        const setTokenlist = async () => {
            await api.vault.getBalanceList(vaultcontract).then(listArr => {
                if (!listArr) return;
                setList(listArr.data)
            });
           
            // });
        };
        setTokenlist();
    }, [vaultcontract,erc20contract]);

    useEffect(() => {
        if (!deposit) return;
        let obj = {
            amount:amount, selected
        }

        const setdeposittrs = async () => {
            await api.vault.deposit(vaultcontract, obj, (result) => {
                if (result) {
                    clearSelected();
                    // props.handleClose();
                    // props.setShow();
                    setdeposit(false)
                }
            }).catch((error) => {
                dispatch({ type: 'MSGTYPE', payload: { msg: `${t('vault.deposit')}: ${error.message}` } });

                dispatch({ type: 'LOADINGTYPE', payload: null });
            });

        };
        setdeposittrs();

    }, [deposit]);

    const handleChange = (e) => {
        const { value } = e.target;
        setAmount(value)
    }

    const clearSelected = () =>{
        setAmount('')
        setSelected('')
        dispatch({ type: 'LOADINGTYPE', payload: null });
        dispatch({ type: 'BALANCE', payload: true });
        props.setShow();
    }
    const handleConfirm = async () => {
        if (!amount || Number(amount) <= 0) return
        if (!selected && !defaultSelectAddress) return

        props.handleClose()
        dispatch({ type: 'LOADINGTYPE', payload: t('vault.Createdeposit') });

        try {
            const signer = web3Instance.getSigner(Account)
            // approve
            const erc20 = await api.erc20.getContract(web3Instance, selected || defaultSelectAddress);
            const deci = await erc20.decimals()

            let allowanceAmount = await erc20.connect(signer).allowance(Account, daoContractAddress.VaultManager);
            let allowance = Number(ethers.utils.formatUnits(allowanceAmount, deci))
            if (!allowance || allowance < amount) {
                const res = await erc20.connect(signer).approve(daoContractAddress.VaultManager, ethers.utils.parseUnits(String(Number(amount)*2), deci), { gasLimit: 50000 });
                await res.wait()
            }
            await api.vault.transfer(daoOrg, signer, {
                tokenAddress: selected || defaultSelectAddress,
                account: Account,
                amount: ethers.utils.parseUnits(amount, deci),
                deposit: true
            })
            clearSelected()
    
        } catch (error) {
            console.error(error)
            const msg = (error && error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('vault.deposit')}: ${msg}` } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }

        // let Num;
        // if( selected === window.mainAddress.native_token){
        //     Num = amount * Math.pow(10,decimals)
        //     await api.main.transferHeader(api,vaultcontract.address.toHuman(), Num,(result)=>{
        //         clearSelected()
        //     }).catch((error)=>{
        //         dispatch({ type: 'MSGTYPE', payload: { msg: `Transfer: ${error.message}` } });
        //         dispatch({ type: 'LOADINGTYPE', payload: null });
        //     });
        // }else{
        //     Num = amount;

        //     await api.erc20.approveOp(erc20contract, vaultcontract.address.toHuman(), Num, (result) => {
        //         setdeposit(true);
        //     }).catch((error) => {
        //         dispatch({ type: 'MSGTYPE', payload: { msg: `Deposit approve: ${error.message}` } });
        //         dispatch({ type: 'LOADINGTYPE', payload: null });
        //     })
        // }

    }
    const handleSelect = (value) => {
        setSelected(value)
    }
    const CloseClear = () =>{
        setSelected(null)
        props.handleClose()
    }

    useEffect(()=>{
        setList(props.tokenlist)
        if (props.tokenlist.length > 0) {
            setDefaultSelectAddress(props.tokenlist[0].address)
        }
    }, [props.tokenlist])


    useImperativeHandle(ref, () => ({
        resultToVault: () => {
            return {
                selected,
                amount
            }
        },
        amountToNull: () => {
            setAmount('')
        }
    }));

    let { handleClose, showTips } = props;
    return (
        <div>
            <Modal visible={showTips} onCancel={CloseClear} footer={null}>
                <div className="title">
                    {/* <img src={sender} alt="" /> */}
                    <span>Deposit</span>
                </div>

                <div className="label">Token</div>
                <div className="inputBrdr">
                    <Select style={{ width: '100%' }} onChange={handleSelect} placeholder={t('vault.Token')} key={defaultSelectAddress} defaultValue={defaultSelectAddress}>
                        {
                            list.map((i) =>
                                <Select.Option value={i.address} key={i.address}>
                                    <div style={{ display:'flex', alignItems:'center'}}>
                                        <div style={{ display:'flex', flexDirection:'column', flexGrow:1}}>
                                            <div style={{lineHeight: '3.55rem',fontFamily: 'HelveticaNeue',fontWeight: 400,textTransform:'uppercase', color: '#10164B'}}>{i.symbol}</div>
                                            <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${i.address.slice(0,5)}…${i.address.slice(-5)}`}</div>
                                        </div>
                                        <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>{t('vault.Available')} : {i.balance}</div>
                                    </div>
                                </Select.Option>
                            )
                        }
                    </Select>
                </div>

                <div className="label">{t('vault.Amount')}</div>
                <div className="inputBrdr">
                    <Input
                        placeholder={t('vault.Amount')}
                        value={amount}
                        name='amount'
                        type="number"
                        onChange={handleChange}
                    />
                </div>

                <Button type="primary" disabled={!amount} onClick={() => handleConfirm()} style={{ width: '100%', marginTop: '3.9rem' }}>{t('vault.Confirm')}</Button>
            </Modal>
        </div>
    )
})
export default Deposit
