const reqGateway = async ()=>{
    try {
        return await getGateway()
    } catch (error) {
        console.error('get gateway failed', error)
        return ''
    }
}
const useDaos = (cat) => {
    const { state } = useWeb3();
    const { maincontract, ChainID } = state;

    const [list, setList] = useState([]);

    const getBestDaos = useCallback(async(contract, url, key, cat)=>{
        const data = await getJsonFile(url)
        if (!data || !data[key]){
            return []
        }
        const ids = data[key][cat]
        const infoList = []
        const gateway = await reqGateway()
        for (const id of ids) {
            const { name, logo, desc } = await api.dao.getDaoBaseInfo(contract, id)
            infoList.push({name, logo: gateway+logo, desc, id})
        }
        setList(infoList)
    }, [])

    useEffect(()=>{
        if (ChainID && maincontract) {
            const networkData = getNetworkData(ChainID)
            getBestDaos(maincontract, networkData.daos.url, networkData.daos.key, cat)
        }
    }, [ChainID, maincontract])
    return list
}

