import { forwardRef, useState, useImperativeHandle } from "react"
import { InputNumber } from "antd"
import styled from "styled-components"
import {useTranslation} from "react-i18next";
import { VentureSubTitle } from "./style"

const VoteConfigBox = styled.div`
    .vote-tip {
        margin-bottom: 1rem;
        font-size: 1.8rem;
    }
    .vote-light-tip {
        line-height: 2.2rem;
        margin-bottom: 1rem;
        color: #A6A6B9;
    }
    .min-support {
        width: 14rem !important;
        margin-right: 1rem;
        &.ant-input-number {
            border: none;
        }
    }
`
const ConfigHead = styled.div`
    font-size: 3.4rem;
    margin-top: 6.3rem;
    color: #10134E;
    line-height: 4rem;
    margin-bottom: 0.9rem;
`

const VentureVoteConfig = forwardRef((props, ref)=>{
    const { t } = useTranslation();
    const { gp } = props
    const [minSupportNumber, setMinSupportNumber] = useState()

    useImperativeHandle(ref, () => ({
        checkInvalid: ()=>{
            return !gp || !minSupportNumber || minSupportNumber <= 0
        },
        newVoteConfig: {
            minRequire: gp,
            minSurportRequire: minSupportNumber
        },
        getVoteConfig: ()=>{
            return {
                minNumber:gp,  minSupportNumber
            }
        }
        
    }));
    return (
        <VoteConfigBox className="line">
            {
                props.titleClass
                    ? <>
                        <div className={props.titleClass}>{t('vote.VoteConfiguration')}</div>
                        <div className="vote-tip">{t('vote.VoteConfigTip')}</div>
                    </>
                    : <>
                        <VentureSubTitle>
                            <span></span>
                            {t('vote.VoteConfiguration')}
                        </VentureSubTitle>
                        <div className="vote-tip vote-light-tip">{t('vote.VoteConfigTip')}</div>
                    </>
            }
            
            <div className="inputBrdr input-line">
                <InputNumber 
                    className="min-support" 
                    name='minSupportNumber'
                    min={1}  
                    value={minSupportNumber} 
                    onChange={v=>setMinSupportNumber(Math.floor(v))} 
                    placeholder={t('create.enter')}
                />
                <span>{t('vote.VoteConfigNumber', {gp})}</span>
            </div>
        </VoteConfigBox>
    )
})
export default VentureVoteConfig