import React, {useEffect, useState} from 'react';
import {useTranslation} from "react-i18next";
import PublicJS from "../../utils/publicJs";
import Ongoing from '../../img/Ongoing.png';
import Passed from '../../img/Passed.png';
import Rejected from '../../img/Rejected.png';
import Expired from '../../img/Expired.png';
import styled from 'styled-components';
import { Empty } from 'antd'

const Status = styled.span`
    display: flex;
    justify-content: flex-start;
    img{
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 0.8rem;
    }
`

export default function VotePagination(props){
    const [indexList, setIndexList] = useState([]);
    let { t } = useTranslation();

    useEffect(() => {
        if(props.list.length){
            setIndexList(props.list)
        }

    }, [props]);
    const handleClicktoVoteview = (voteid) => {
        let { handle, owner } = props;
        props.history.push(`/voteOverview/${handle}/${voteid}`)
    }
        return (
            <div className='votePending'>
                <table className="myTable votePending">
                    <thead>
                    <tr>
                        <th>{t('vote.Number')}</th>
                        <th>{t('vote.Time')}</th>
                        <th>{t('vote.Title')}</th>
                        <th>{t('vote.Status')}</th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        indexList.map((item, index)=><tr key={`history_${item.vote_id}`} onClick={()=>handleClicktoVoteview(item.vote_id)}>
                            <td>{index + 1}</td>
                            <td>{item.endDisplay}</td>
                            <td>{item.title}</td>
                            <td>
                                {/*{*/}
                                {/*    item.status === '0' &&<Status>*/}
                                {/*        <img src={Ongoing} alt=""/>*/}
                                {/*        <span>Ongoing</span>*/}
                                {/*    </Status>*/}
                                {/*}*/}
                                {
                                    item.status === 1 ?
                                    (
                                        <Status>
                                            <img src={Passed} alt=""/>
                                            <span>{t('vote.Passed')}</span>
                                        </Status>
                                    ):(
                                        <Status>
                                            <img src={Rejected} alt=""/>
                                            <span>{t('vote.Failed')}</span>
                                        </Status>
                                    )
                                }
                                {/*{*/}
                                {/*    !item.executed &&<Status>*/}
                                {/*        <img src={Rejected} alt=""/>*/}
                                {/*        <span>Rejected</span>*/}
                                {/*    </Status>*/}
                                {/*}*/}

                            </td>
                        </tr>)
                    }
                    </tbody>
                </table>
                {
                    indexList.length === 0 && <Empty style={{padding: '1.2rem', margin: 0, background: '#fff'}} image={Empty.PRESENTED_IMAGE_SIMPLE} />
                }
            </div>
        )
}


