import React from 'react';
import Home from '../home';
import About from '../about';
import Createnew from '../createNew';
import Vote from '../vote';
import NewVote from '../components/vote/newVote';
import VoteView from '../components/vote/voteView';
import VoteOverview from '../components/vote/voteOverview';
import Vault from '../vault';
import Deposit from '../components/vault/deposit';
import Withdraw from '../components/vault/withdraw';
import Org from '../org';
import Venture from '../venture';
import VenturePanel from '../venturePanel';
import Registration from '../Registration';

import { HashRouter as Router, Route, Switch, Redirect } from "react-router-dom";

function routerlink() {
    return (
        <Switch>
            {/*<Route path="/" component={Home} exact/>*/}

            <Route path="/home" component={Home} />
            <Route  path="/about/:handle" component={About}/>
            {/*<Route   activeClassName="active"  path="/about/:id" component={About}/>*/}
            <Route path="/create" component={Createnew} />
            <Route path="/Vote/:handle" component={Vote} />
            {/* <Route path="/newVote" component={NewVote} /> */}
            <Route path="/newVote/:handle" component={NewVote} />
            <Route path="/voteView/:handle/:voteid" component={VoteView} />
            <Route path="/voteOverview/:handle/:voteid" component={VoteOverview} />
            <Route path="/vault/:handle" component={Vault} />
            <Route path="/deposit/:handle" component={Deposit} />
            <Route path="/withdraw/:handle" component={Withdraw} />
            <Route path="/org/:handle" component={Org} />
            <Route path="/venture/:handle" component={Venture} />
            <Route path="/venturePanel/:handle" component={VenturePanel} />
            <Route path="/registration" component={Registration} />
            <Redirect from="/" to="/home" exact />
        </Switch>
    );
}

export default routerlink;
