export default function VenturePanel(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { daoContractAddress, stockcontract, Account, maincontract, whoAmI, venturecontract, web3Instance} = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [fund, setFund] = useState({
        address: '',
    })
    const [mintToken, setMintToken] = useState({})
    const [share, setShare] = useState({})

    const [manageFee, setManageFee] = useState(0)
    const [GpBonus, setGpBonus] = useState(0)
    const [period, setPeriod] = useState()


    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);


    useEffect(()=>{
        if (!daoContractAddress || !stockcontract) return
        const getStockToken = async()=>{
            const deci = await stockcontract.decimals()
            let mintToken = {
                deci: deci,
                address: daoContractAddress['VenturesStockManager'],
                symbol: await stockcontract.symbol(),
                balance: ethers.utils.formatUnits(await stockcontract.totalSupply(), deci),
                userBalance: ethers.utils.formatUnits(await stockcontract.balanceOf(Account), deci)
            }
            mintToken.userBalanceDisplay = getShortDisplay( mintToken.userBalance)
            mintToken.balanceDisplay = getShortDisplay( mintToken.balance)
            setMintToken(mintToken)
        }
        getStockToken()
    }, [daoContractAddress, stockcontract])

    useEffect(()=>{
        if (!venturecontract) return
        const getfund = async()=>{
            try {
                const {raised, goal, address} = await api.venture.getFundInfo(venturecontract)
                const tokenContract = await api.erc20.getContract(web3Instance, address)
                const symbol = await tokenContract.symbol()
                setFund({
                    raised, goal, symbol, address
                })
            } catch (error) {
                console.error("getfund failed", error)
            }
            
        }
        getfund()
    }, [venturecontract])

    useEffect(()=>{
        if (!venturecontract) return 
        const getOthers = async()=>{
            try {
                const fee = await venturecontract.ManageFeePercent()
                setManageFee((fee.toNumber()/100).toFixed(2))
            } catch (error) {
                console.error('get ManageFeePercent failed', error)
            }
            try {
                const bonus = await venturecontract.totalGPBonusPercent()
                setGpBonus((bonus.toNumber()/100).toFixed(2))
            } catch (error) {
                console.error('get totalGPBonusPercent failed', error)
            }
        }
        getOthers()
    }, [venturecontract])

    useEffect(()=>{
        if (!fund || !fund.symbol) return
        const getYourDeposit = async()=>{
            try {
                // TODO
                setShare({
                    // value: mintToken.userBalance,
                    symbol: fund.symbol
                })
            } catch (error) {
                
            }
        }
        getYourDeposit()
    }, [fund, mintToken])

    
    useEffect(()=>{
        if (!venturecontract) return
        const getVenturePeriod = async()=>{
            try {
                const period = await venturecontract.getVenturePeriod()
                setPeriod(period+1)
            } catch(error) {
                console.error('getVenturePeriod failed', error)
            }
        }
        getVenturePeriod()
    }, [venturecontract])

    const updateDaoID = useCallback(
        id=>setAId(id),
        []
    )

   
    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip className='font-light'>{errorTips}</Tip>
            </Modal>

            <div className='container'>
                <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} />
                <Section>
                    <div className="titleTop">{t('venture.Invest')}</div>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>{t('venture.Mint')}</div>
                            <div className='value1'>{mintToken.balanceDisplay}</div>
                            <div className='symbol'>{mintToken.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>{t('venture.Fund')}</div>
                            <div className='value1'>{fund.address.slice(0, 6)}...{fund.address.slice(-6)}</div>
                            <div className='symbol'>{fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>{t('venture.ManageFee')}</div>
                            <div className='value2'>{manageFee}%</div>
                        </LI>
                        <LI>
                            <div className='title'>{t('venture.GPBouns')}</div>
                            <div className='value2'>{GpBonus}%</div>
                        </LI>
                    </Ul>
                </Section>
                <YourShare mintToken={mintToken} share={share}></YourShare>
                { period === 5 && <ProjectList id={props.match.params.id} venturecontract={venturecontract} isOwner={isOwner} fundSymbol={fund.symbol}></ProjectList> }
            </div>
            <NFTlist id={props.match.params.id}></NFTlist>
        </div>
    )
}
