import { forwardRef, useState, useImperativeHandle } from "react"
import { Input, InputNumber } from "antd"
import styled from "styled-components"
import {useTranslation} from "react-i18next";

const VoteConfigBox = styled.div`

`
const ConfigHead = styled.div`
    font-size: 3.4rem;
    margin-top: 6.3rem;
    color: #10134E;
    line-height: 4rem;
    margin-bottom: 0.9rem;
`

const VoteConfig = forwardRef((props, ref)=>{
    let { t } = useTranslation();
    const [minNumber, setMinNumber] = useState()
    const [minSupportNumber, setMinSupportNumber] = useState()

    const handleInputChange = (e) => {
        const { name, value } = e.target
        switch(name) {
            case "minNumber": 
                setMinNumber(Number(value));
                break
            case "minSupportNumber": 
                setMinSupportNumber(Number(value));
                break
            default:
                return
        }
    }

    useImperativeHandle(ref, () => ({
        checkInvalid: ()=>{
            return !minNumber || !minSupportNumber || minNumber <= 0 || minSupportNumber <= 0
        },
        getVoteConfig: ()=>{
            return {
                minNumber, minSupportNumber
            }
        }
        
    }));
    return (
        <VoteConfigBox>
            <ConfigHead>{t('vote.VoteConfiguration')}</ConfigHead>
            <div className="line">
                <div className="title">{t('vote.mini')}</div>
                <div className='inputBrdr input-line'>
                    <Input
                        placeholder={t('create.enter')}
                        name='minNumber'
                        value={minNumber}
                        min={1}
                        type="number"
                        onChange={handleInputChange}
                    />
                </div>
            </div>

            <div className="line">
                <div className="title">{t('vote.support')}</div>
                <div className='inputBrdr input-line'>
                    <Input
                        placeholder={t('create.enter')}
                        name='minSupportNumber'
                        value={minSupportNumber}
                        min={1}
                        type="number"
                        onChange={handleInputChange}
                    />
                </div>
                
            </div>
        </VoteConfigBox>
    )
})
export default VoteConfig