import React, { useEffect, useState, useCallback } from "react";
import {Button, Modal, DatePicker, InputNumber} from "antd";
import styled from "styled-components";
import moment from 'moment';
import { useWeb3 } from "../../api/contracts";
import Estimate from "./estimate";
import api from "../../api";
import { ethers } from "ethers"
import {useTranslation} from "react-i18next";


const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`


export default function RaiseGps(props) {
    const { show ,closeRaise, fund } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg } = state;

    const [ratio, setRatio] = useState(null)
    const [maxDep, setMaxDep] = useState(null)
    const [minDep, setMinDep] = useState(null)

    const [raiseTime, setRaiseTime] = useState(null)
    const [eHeight, setEHeight] = useState(null)

    const [maxRaised, setMaxRaised] = useState(0)

    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }

    const handleInputChange = (e) =>{
        const {name, value} = e.target
        switch (name) {
            case 'ratio':
                setRatio(Number(value))
                break
            case 'maxDep':
                setMaxDep(Number(value))
                break
            case 'minDep':
                setMinDep(Number(value))
                break
            default:
                return
        }
    }

    const handleRaiseTime = (v)=>{
        const dateTime = Date.parse(v._d)
        // timestamp(s)
        setRaiseTime(Math.round(dateTime/1000))
    }

    const handleClose = () =>{
        closeRaise()
    }

    const confirmRaise = async ()=>{
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.ConfigGP')}` });

            const erc20 = await api.erc20.getContract(web3Instance, fund.address);
            const deci =  await erc20.decimals()

            const signer = web3Instance.getSigner(Account)
            await api.venture.configGPraise(daoOrg, signer, {
                height: raiseTime, 
                ratio: ratio*100, 
                minDep: ethers.utils.parseUnits(String(minDep), deci), 
                maxDep: ethers.utils.parseUnits(String(maxDep), deci), 
            })
            props.getVenturePeriod()
        } catch (error) {
            console.error(error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.ConfigFailed')}: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    useEffect(()=>{
        if (!fund.hard || !ratio) return
        setMaxRaised(Number(fund.hard)*ratio/100)
    }, [fund, ratio])

    const formatDate = useCallback((m)=>{
        return m.format('YYYY-MM-DD HH:mm:ss') + ' GMT' + m.format('ZZ')
    }, [])

    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >{t('venture.RaiseGP')}</span>
        </div>

        <div className="label">{t('venture.RaiseTime')}</div>
        <div className="inputBrdr">
            <DatePicker
                className="myDatePicker"
                placeholder={t('venture.RaiseTime')}
                style={{ width: '100%' }}
                format={formatDate}
                allowClear={false}
                disabledDate={disabledDate}
                onChange={handleRaiseTime}
                showTime={true}
                showNow={false} />
        </div>
        {/* <Estimate time={raiseTime} updateHeight={v=>setEHeight(v)} /> */}
            
        <div className="label">{t('venture.RaiseRatio')}</div>
        <div className="inputBrdr">
            <InputNumber 
                placeholder={t('venture.RaiseRatio')}
                controls={false}
                precision={2}
                onChange={(v)=>setRatio(v)}
            />
            <span className='suffix'>%</span>
        </div>

        <div className="label">{t('venture.MaxGP')}</div>
        <div className="inputBrdr">
            <InputNumber 
                placeholder={t('venture.MaxGP')}
                controls={false}
                onChange={(v)=>setMaxDep(v)}
            />
            <span className='suffix'>{t('venture.Max')}: {maxRaised} {fund.symbol}</span>
        </div>
        <div className="label">{t('venture.MinGP')}</div>
        <div className="inputBrdr">
            <InputNumber 
                placeholder={t('venture.MinGP')}
                controls={false}
                onChange={(v)=>setMinDep(v)}
            />
            <span className='suffix'>{props.symbol}</span>
        </div>

        <NextBrdr >
            <Button type="primary" disabled={!minDep || !maxDep || !ratio || !raiseTime || maxDep>maxRaised} style={{ width: '100%', marginTop: '3rem' }} onClick={confirmRaise}>
                {t('venture.Submit')}
            </Button>
        </NextBrdr>
    </ModalBox>
}
