import React from 'react';
import styled from 'styled-components';

const Breadcrumbstep = styled.div`
    margin-top: 3rem;
    margin-bottom: 8.2rem;
    ul{
        display: flex;
        justify-content: space-between;
        padding: 0 2rem;
    }
    li {
        width: 4.5rem;
        height: 4.5rem;
        text-align: center;
        position: relative;
        .dot {
            display: inline-block;
            width: 1.5rem;
            height: 1.5rem;
            background: #A6A6B7;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -0.75rem;
            margin-top: -0.75rem;
            &.finished {
                background: #D51172;
            }
            &.circle {
                display: none;
            }
        }
        &.active {
            .circle {
                display: block;
                background: #FAE7F0 !important;
                
                animation: scaleDraw 1.5s ease-in-out infinite;
                -webkit-animation: scaleDraw 1.5s ease-in-out infinite;
            }
            @keyframes scaleDraw {
                0%{
                    transform: scale(2);
                }
                10%{
                    transform: scale(2.1);
                }
                20%{
                    transform: scale(2.2);
                }
                25%{
                    transform: scale(2.4);
                }
                30%{
                    transform: scale(2.5);
                }
                40%{
                    transform: scale(2.8);
                }
                50%{
                    transform: scale(3);
                }
                60%{
                    transform: scale(3.5);
                }
                70%{
                    transform: scale(3);
                }
                80%{
                    transform: scale(2.8);
                }
                90%{
                    transform: scale(2.5);
                }
                95%{
                    transform: scale(2.3);
                }
                100%{
                    transform: scale(2);
                }
            }
        }
    }
`
export default function NewVoteTop(props) {
    return <div >
        <Breadcrumbstep >
            <ul>
                {
                    [1,2,3,4,5].map(i => (
                        <li key={i} className={`step1 ${props.type === i ? 'active' : ''} ${props.type > i ? 'after' : ''}`}>
                            <span className='dot circle'></span>
                            <span className={props.type >= i ? 'dot finished' : 'dot'}></span>
                        </li>
                    ))
                }
            </ul>
        </Breadcrumbstep>

    </div>;
}

