import React, { useEffect, useState, useCallback } from 'react';
import VotePagination from './components/vote/votePagination';
import VotePending from './components/vote/votePending';
import VoteActive from './components/vote/voteActive';

import { useWeb3 } from "./api/contracts";

import api from './api/index';
import Left, { DaoType } from './components/left';
import { useTranslation } from "react-i18next";
import { Button } from "antd";
import styled from "styled-components";
import LoadingNew from "./components/loadingNEW";

import PublicJS from './utils/publicJs';

const FirstLine = styled.div`
  position: relative;
`;
const BtnRht = styled.div`
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    align-content: center;
    padding-right: 3rem;
    button{
        width: 12rem;
        height: 4rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        font-size: 1.8rem;
        margin-left: 2rem;
        padding: 0;
        line-height: 3rem;
        &.active{
            width: 12rem;
            height: 4rem;
            border-radius: 0.4rem;
            border: 0.1rem solid #D52473;
            color: #d52473;
        }
    }
`;

const Tablesec = styled.section`
  width: 96rem;
`;
const TablesecHistory = styled.section`
  width: 96rem;
`;

export default function Vote(props) {

    const { state, dispatch } = useWeb3();
    const { isVentureDao, web3Instance, Account, votecontract, apiState, erc20contract, orgcontract,whoAmI,maincontract } = state;

    const [id, setAId] = useState(null);
    const [activelist, setActivelist] = useState([]);
    const [pendinglist, setPendinglist] = useState([]);
    const [historylist, setHistorylist] = useState([]);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);


    const [showPending, setShowPending] = useState(true);
    const [showActive, setShowActive] = useState(true);
    const [showHistory, setShowHistory] = useState(true);

    let { t } = useTranslation();

    useEffect(() => {
        console.log('whoAmI:', whoAmI)
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(async()=>{
        if (!votecontract)return
        setAll()
    }, [votecontract])

    const requestVote = async (votecontract, vid) => {
        const vdata = await api.vote.getVote(votecontract, vid)
        vdata.vote_id = vid
        if (vdata.isExecuted) vdata.status = 1  // 1 means passed

        if (await votecontract.isHistoryVote(vid)){
            vdata.category = 'history'
        } else if (await votecontract.isActiveVote(vid)) {
            vdata.category = 'active'
        } else if (await votecontract.isPendingVote(vid)) {
            // NOTE: which is pending but not can be Executed will push to history
            const canExecute = await votecontract.canExecute(vid)
            vdata.category = canExecute.toString() === '0' ? 'pending' : 'history'
            vdata.status = canExecute ? 2 : 0
        }
        return vdata
    }

    const setAll = async () => {
        // get vote length
        const len = await api.vote.getVotesLength(votecontract)

        let historyVotes = [];
        let activeVotes = [];
        let pendingVotes = [];

        let dataReqs = []
        for (let i=0; i<len; i++) {
            dataReqs.push(requestVote(votecontract, i))
        }
        let res = await Promise.all(dataReqs)
        res.forEach(vdata=>{
            if (vdata.category === 'history') {
                historyVotes.push(vdata);
            } else if (vdata.category === 'active') {
                activeVotes.push(vdata);
            } else if (vdata.category === 'pending') {
                pendingVotes.push(vdata);
            } 
        })

        setActivelist(activeVotes);
        setShowActive(false);

        setPendinglist(pendingVotes);
        setShowPending(false);

        setHistorylist(historyVotes)
        setShowHistory(false)

    };

    const updateDaoID = useCallback(
        id=>setAId(id),
        []
    )

    const clickNewVote = async () => {
        if (!votecontract) return
        try {
            const voteFuncStatus = await votecontract.isOpenVote()
            if (!voteFuncStatus) {
                dispatch({ type: 'MSGTYPE', payload: { msg: t('vote.VoteDisabled'), closable: true } });
                return
            }
        } catch (error) {
            console.error('isOpenVote failed')
        }

        props.history.push(`/newVote/${props.match.params.handle}?url=${props.match.url}${isVentureDao ? '&venture=1' : ''}`)
    }

    return (
        <div>
            <div className="container">
                <FirstLine>
                    <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} owner={props.match.params.owner} />
                    <BtnRht>
                        {
                            (isModerator || isOwner || (isVentureDao && isMember)) && Account &&
                            <Button type="primary" onClick={clickNewVote}>{t('vote.new')}</Button>
                        }
                    </BtnRht>
                </FirstLine>
                <Tablesec>
                    <div className="titleTop">{t('vote.Vote')}</div>
                    {
                        !showActive && <VoteActive
                            id={id}
                            handle={props.match.params.handle}
                            list={activelist}
                            history={props.history}
                            owner={props.match.params.owner}
                        />
                    }
                    {
                        showActive &&<LoadingNew  />
                    }

                </Tablesec>
                <Tablesec>
                    <div className="titleTop">{t('vote.Waiting')}</div>
                    {
                        !showPending && <VotePending
                            id={id}
                            handle={props.match.params.handle}
                            list={pendinglist}
                            history={props.history}
                            refresh={setAll}
                            owner={props.match.params.owner}
                        />
                    }
                    {
                        showPending && <LoadingNew  />
                    }

                </Tablesec>

                <TablesecHistory>
                    <div className="titleTop">{t('vote.Finished')}</div>
                    {
                        !showHistory && <VotePagination
                            id={id}
                            handle={props.match.params.handle}
                            list={historylist}
                            history={props.history}
                            owner={props.match.params.owner}
                        />
                    }
                    {
                        showHistory &&<LoadingNew  />
                    }

                </TablesecHistory>
            </div>

        </div>
    )

}

