import React, { useCallback, useEffect, useState } from "react";
import { Button, Input, Modal, Upload } from "antd";
import styled from "styled-components";
import { Translation, useTranslation } from "react-i18next";
import { useCSVReader } from 'react-papaparse';
import { ethers } from "ethers"
import goMerklizedSetup, { createAndUpload } from '../venture/whitelist'
import { useWeb3 } from "../../api/contracts";
import { getGateway, getJsonFile } from "../../api/apiHTTP"


const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
     .textareaB.inputBrdr{
        textarea.ant-input{
          padding: 2.5rem;
          box-sizing: border-box;
             height: 25.7rem!important;
        }
    }
    .ant-upload-list-item-error, .ant-upload-list-item-error .ant-upload-text-icon > .anticon, .ant-upload-list-item-error .ant-upload-list-item-name,.ant-upload-list-item-error .ant-upload-list-item-card-actions .anticon{
    color: #D52473;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`

const ChooseBtn = styled(Button)`
    border-radius: 1rem;
    border: 0.1rem solid #D52473;
    font-size: 1.8rem;
      font-family: "Roboto-Regular";
    font-weight: 400;
    color: #D52473;
    padding:1rem 1.6rem 0.6rem;
    height: 4.8rem;
`

const FileTip = styled.span`
    color: grey;
    font-size: 1.2rem;
    margin-left: 2rem;
`

export default function Whitelist(props) {
    const { show, closeModal } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, venturecontract } = state;

    const [addressInput, setAddressInput] = useState()

    let { t } = useTranslation();

    const { CSVReader } = useCSVReader()

    const getRecords = useCallback(async(code)=>{
        try {
            const gateway = await getGateway()
            const url = gateway+code
            const records = await getJsonFile(url)
            return (records || []).map(r=>r.address)
            
        } catch (error) {
            console.error(error)
            return []
        }
    }, [])

    useEffect(async()=>{
        if (!show || !venturecontract) return
        try {
            const getRecordsCode = async()=>{
                return await venturecontract.whiteListDataURI()
            }
            const code = await getRecordsCode()
            if (!!code) {
                const addressList = await getRecords(code)
                setAddressInput(addressList.join('\n'))
            }
        } catch (error) {
            console.error('getRecords failed', error)
        }
        
    }, [show, venturecontract])


    const propsObj = {
        name: 'whitelist',
        onChange(info) {
            if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
                console.log(`${info.file.name} file uploaded successfully`);
            } else if (info.file.status === 'error') {
                console.error(`${info.file.name} file upload failed.`);
            }
        },
        beforeUpload() {
            return false
        }
    };

    const handleCSVfile = (data) => {
        const addressList = []
        data.forEach(address => {
            console.log('address: ', address, ethers.utils.isAddress(address[0]))
            if (ethers.utils.isAddress(address[0])) addressList.push(address[0])
        });

        const arr = Array.from(new Set(addressList))
        setAddressInput(arr.join('\n'))
    }
    

    const submit = async () => {
        console.log(addressInput)
        const arr = addressInput.split('\n')
        const addressList = []
        arr.forEach(a => {
            if (ethers.utils.isAddress(a.trim())) addressList.push(a)
        })
        if (addressList.length === 0) {
            return
        }

        try {
            // upload
            const { root, records } = goMerklizedSetup(addressList)
            const recordsCode = await createAndUpload(records)
            console.log('[createAndUpload] recordsCode: ', recordsCode)
            props.updateWhiteList({root, recordsCode})
            handleClose()
        } catch (error) {
            console.error('modifyWhiteList failed: ', error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.WhiteListError')}: ${msg}`, closable: true } });
        }
    }


    const handleClose = () => {
        closeModal()
    }
    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span>{t('venture.AddLP')}</span>
        </div>
        <div>
            <CSVReader
                onUploadAccepted={(results) => { handleCSVfile(results.data) }}
            >
                {/* <ChooseBtn> {t('venture.ChooseFile')}</ChooseBtn> */}
                {({
                    getRootProps,
                }) => (
                    <>
                        <div
                            {...getRootProps()}
                            className="rht"
                        >
                            <ChooseBtn> {t('venture.ChooseFile')}</ChooseBtn>
                            <FileTip>{t('venture.whilelistFileTip')}</FileTip>
                        </div>
                    </>
                )}
            </CSVReader>
            {/* <Upload {...propsObj}>
                <ChooseBtn> {t('venture.ChooseFile')}</ChooseBtn>
            </Upload> */}

        </div>

        <div className="label">{t('venture.Addresses')} </div>
        <div className='inputBrdr textareaB'>
            <Input.TextArea
                name='Address'
                value={addressInput}
                placeholder={t('venture.enter')}
                onChange={(e) => setAddressInput(e.target.value)}
            />
        </div>

        <NextBrdr >
            <Button type="primary" style={{ width: '100%', marginTop: '3rem' }} disabled={!addressInput} onClick={submit}>
                {t('venture.Submit')}
            </Button>
        </NextBrdr>

    </ModalBox>
}
