const buyToken = async (daoOrg, signer, obj) => {
    const templateAction = ethers.utils.defaultAbiCoder.encode(
        ['address', 'bytes'],
        [
            window.mainAddress.actionVaultUniswapV2SwapToken,
            ethers.utils.defaultAbiCoder.encode(
                // address router, address WETH, address tokenIn, address tokenOut, uint256 amountIn, uint256 amountOutMin
                ['address', 'address', 'address', 'uint256', 'uint256'],
                [Router, obj.tokenIn, obj.tokenOut, obj.amountIn, obj.amountOut]
            )
        ]
    );
    const res = await daoOrg.connect(signer).action(templateAction);
    await res.wait()
}

const withdraw = async (daoOrg, signer, amount)=>{
    const templateAction = ethers.utils.defaultAbiCoder.encode(
        ['address', 'bytes'],
        [
            window.mainAddress.actionVenturesClaimShare,
            ethers.utils.defaultAbiCoder.encode(
                ['uint256'],
                [amount]
            )
        ]
    );
    const res =  await daoOrg.connect(signer).action(templateAction);
    await res.wait()
}


const getClaimEarningEstimate = async(web3Instance, signer, obj)=>{
    const contract = await getActionContract(web3Instance)
    return await contract.connect(signer).claimEarningEstimate(obj.account, obj.template, 0,)
}

const getAmountOutMin = async(web3Instance, signer, obj)=>{
    const contract = await getSwapContract(web3Instance)
    return await contract.connect(signer).getAmountOutMin(Router, obj.tokenIn, obj.tokenOut, obj.amountIn)
}

const getFundInfo = async (contract) => {
    return {
        raised: (await contract.getRaisedAmount()).toString(),
        goal: (await contract.RaiseMinSoftCap()).toString(),
        hard: (await contract.RaiseMaxHardCap()).toString(),
        address: await contract.TokenType()
    }
}

