import React, { useState, forwardRef, useImperativeHandle, useRef } from 'react'
import { Input, Select } from 'antd'
import { useTranslation } from 'react-i18next';
import { SelectShortBrdr, GpInfo } from './styles'
import publicJs from '../../../utils/publicJs';
import VentureVoteConfig from '../../create/ventureVoteConfig'

const KickGP = React.memo(forwardRef((props, ref)=>{
    const { gplist, updateGplist } = props
    const { t } = useTranslation();

    const voteRef = useRef();

    const [selectGP, setSelectGP] = useState(null)

    const handleSetBonus = (e, index)=>{
        let newArray = [...gplist];
        const { name, value } = e.target;
        newArray[index].bonus = Number(value);
        updateGplist(newArray)
    }


    useImperativeHandle(ref, () => ({
        checkValid() {
            return selectGP && !voteRef.current.checkInvalid()
        },
        getData() {
            const gp_address = [], gp_bonus = []
            gplist.forEach(l=>{
                if (l.address !== selectGP) {
                    gp_address.push(l.address)
                    gp_bonus.push(l.bonus*100)
                }
            })
            return {
                gpAddress: selectGP,
                gpList: gp_address, 
                gpBonus: gp_bonus, 
                newVoteConfig: voteRef.current.newVoteConfig
            }
        }
    }));

    return <div className='venture-line'>
        <div className="line">
            <div className='label'>{t('vote.GPAddress')}</div>
        </div>

        <SelectShortBrdr onChange={(v)=>{setSelectGP(v)}}>
            {/* TODO GP address */}
            {
                gplist.map((item, i)=>{
                    return (
                        <Select.Option value={item.address} key={i}>
                            <div className='select-item'>{item.address} </div>
                        </Select.Option>
                    )
                })
            }
        </SelectShortBrdr>
        {
            selectGP && <>
                <GpInfo>
                    <div className="line top">
                        <div className='label'>{t('vote.GpInfo')}</div>
                    </div>
                    <ul>
                        {
                            gplist.map((item, i)=>{
                                return <li key={i} style={{display: item.address === selectGP ? 'none': 'block'}}>
                                            <div className="line">
                                                <div className="label lft"> <span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(item.address)}
        
                                                </div>
        
                                                <div className='inputBrdr rht'>
                                                    <div className="label">{t('vote.bonus')}</div>
                                                    <Input
                                                        placeholder={t('vote.bonus')}
                                                        type="number"
                                                        onChange={(event) => handleSetBonus(event, i)}
                                                        autoComplete="off"/>
                                                    <span className='suffix'>%</span>
                                                </div>
                                            </div>
                                        </li>
                            })
                        }
                    </ul>
                </GpInfo>
                <VentureVoteConfig ref={voteRef} gp={gplist.length-1} titleClass='label' />
            </>
        }
       
    </div>
}))

export default KickGP