import React, { useEffect, useState } from 'react';
import { Button, Modal, Radio,Input } from 'antd';
import { useWeb3 } from "../../api/contracts";
import api from "../../api/index";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { ethers } from "ethers"
import { GpListInfo } from "./voteOverview"
import VoteConfigInfo from "./info/voteInfo"
import LoadingNew from "../loadingNEW";

const NextBrdr= styled.li`
margin-top: 1rem;
     button{
      width: 100%;
      &:disabled{
      opacity: 0.3;
      }
     }
`;

const SectionBrdr = styled.section`
    li{
        margin-bottom: 2rem;
        &>div{
        font-size: 1.8rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        color: #10164B;
        line-height: 1.9rem;
        }
        .ant-radio-group{
        width: 100%;
        }
        .radioBrdr{
             width: 100%;
            padding: 2.1rem 1.5rem;
            border-radius: 1.2rem;
            border: 0.1rem solid #E8E8EA;
            margin-bottom: 1.5rem;
         
                span{
                font-size: 2.1rem;
                font-family: HelveticaNeue;
                font-weight: 300;
                color: #010643;
                }
        }
        .radioActive{
            background: #F9F9FC;
        }
    }
`;
const Titles = styled.span`
  font-size: 1.2rem;
    font-family: HelveticaNeue;
    font-weight: 300;
    color: #10164B;
    opacity: 0.6;
    padding-bottom: 1rem;
    display: inline-block;
    
`;

const Desc = styled.div`
    border-radius: 2.4rem;
`;
const Li = styled.li`
  display: flex;
  word-break: break-all;
`;
const TransferLft = styled.div`
 width: 25rem;
`;
const Amount = styled.div`
   border-left: 0.4rem solid #FFD9CF;;
    font-size: 2rem;
    color: #FF672F;
    line-height: 2em;
    padding-left: 0.8rem;
    text-transform: uppercase;
    
`;
const Addr = styled.div`
  flex-grow: 1;
`

export default function VoteView(props) {

    const { state,dispatch } = useWeb3();
    const { web3Instance, Account, daoOrg, votecontract, vaultcontract, daoContractAddress } = state;

    const [optionlist, setoptionlist] = useState([]);
    const [id, setId] = useState(null);
    const [voteid, setvoteid] = useState(null);
    const [title, settitle] = useState('');
    const [desc, setdesc] = useState('');
    const [selected, setselected] = useState('');
    const [afterchoice, setafterchoice] = useState(false);
    const [show, setShow] = useState(true);
    const [disabledVote, setdisabledVote] = useState(false);
    const [toaddress, settoaddress] = useState('');
    const [toValue, settovalule] = useState('');
    const [active, setActive] = useState(null);
    const [symbol, setSymbol] = useState(null);
    const [voteData, setVoteData] = useState({})
    const [gplist, setGplist] = useState()
    const [newVoteData, setNewVoteData] = useState({})
    const [approveInfo, setApproveInfo] = useState()

    let { t } = useTranslation();

    useEffect(() => {
        if (afterchoice) {
            props.handleClose()
        }

    }, [afterchoice]);

    const setOption = (val) => {
        setselected(val);
        setActive(val)
    }
    const handleRadio = (e) => {
        setOption(e.target.value)
    }

    useEffect(async () => {
        if (votecontract == null || voteid == null) return;
        try {
       
            const result = await Promise.all([
                api.vote.getVote(votecontract, voteid),
                api.vote.getVoterState(votecontract, voteid, Account),
            ])
            const vdata = result[0] 
            if (vdata) {
                setVoteData(vdata)
                settitle(vdata.title);
                setdesc(vdata.desc);
                // setSymbol(erc20_symbol);
                // setId(vote_id);
                // setdesc(desc);
                // settoaddress(to_address);
                setoptionlist(['agree', 'disagree'])
                try {
                    if (vdata.isTransfer) {
                        const wdata = await api.vault.getTransferDetail(vaultcontract, vdata.wid)
                        settoaddress(wdata.proposer)
                        const erc20 = await api.erc20.getContract(web3Instance, wdata.token)
                        setSymbol(await erc20.symbol())
                        settovalule(ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals()))

                    } else if (vdata.isApprove) {
                        const contract = await api.vote.getGrantContract(web3Instance, daoContractAddress["GrantMethodManager"])
                        const data = await contract.registerVotes(voteid)
                        settoaddress(data.gp)
                        if (data.extra) {
                            const result = ethers.utils.defaultAbiCoder.decode(
                                ["address", "address", "uint256"],
                                data.extra
                            )
    
                            try {
                                const erc20 = await api.erc20.getContract(web3Instance, result[0])
                                const symbol = await erc20.symbol()
                                const deci = await erc20.decimals()
                                setApproveInfo({
                                    symbol: symbol,
                                    amount: ethers.utils.formatUnits(result[2], deci)
                                })
                            } catch (error) {
                                console.error(error)
                            }
                        }
                    } else if (vdata.isKick) {
                        const contract = await api.vote.getExecuteContract(web3Instance, daoContractAddress["VoteExecutionManager"])
                        const wdata = await contract.getImpeachGp(voteid)
                        settoaddress(wdata[0])
                        const lst = []
                        for (const i in wdata[3]) {
                            lst.push({
                                address:  wdata[3][i],
                                bonus: wdata[4][i].toNumber() / 100
                            })
                        }
                        setNewVoteData({
                            minRequire: wdata[1].toNumber(),
                            minSuportRequire: wdata[2].toNumber(),
                        })
                        setGplist(lst)
                    } else if (vdata.isNewGp) {
                        const contract = await api.vote.getExecuteContract(web3Instance, daoContractAddress["VoteExecutionManager"])
                        const wdata = await contract.getAcceptGp(voteid)
                        settoaddress(wdata[0])
                        const lst = []
                        for (const i in wdata[4]) {
                            lst.push({
                                address:  wdata[4][i],
                                bonus: wdata[5][i].toNumber() / 100
                            })
                        }
                        setNewVoteData({
                            minRequire: wdata[2].toNumber(),
                            minSuportRequire: wdata[3].toNumber(),
                        })
                        setGplist(lst)
                    } else if (vdata.isTransferManageFee) {
                        const contract = await api.vault.getManageFeeContract(web3Instance, daoContractAddress.ManageFeeVaultManager)
                        const wdata = await contract.withdrawApply(vdata.wid)
                        settoaddress(wdata.proposer)
                        const erc20 = await api.erc20.getContract(web3Instance, wdata.token)
                        setSymbol(await erc20.symbol())
                        settovalule(ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals()))
                    }
                    
                } catch (error) {
                    console.error("getMoreVoteInfo failed", error)
                }
                    
            }
            const voteState = result[1]
            // 0.not vote 1.disagree 2.agree
            if (voteState) {
                setOption(voteState-1)
                setdisabledVote(true)
            }
            setShow(false);
        } catch (error) {
            console.error("[vote failed]", error)
        }
        
    }, [voteid, daoContractAddress]);


    useEffect(() => {
        if (props.id == null) return;
        setvoteid(parseInt(props.voteid));
        setId(props.id);

        const setOnevote = async () => {
            
            // await api.vote.queryOneVote(votecontract, props.voteid).then(data => {
            //     if (!data) return;
            //     console.log("===queryOneVote=",data)
            //     const {
            //         vote_id, title, desc, choices, to_address, transfer_value,erc20_symbol,erc20_address
            //     } = data;
            //     settitle(title);
            //     setSymbol(erc20_symbol);
            //     setId(vote_id);
            //     setdesc(desc);
            //     settoaddress(to_address);
                // settovalule(transfer_value);

                // if(native_token === erc20_address){
                //     let myNum = transfer_value.replace(/,/g,'');
                //     let myValue = parseInt(myNum) / Math.pow(10, decimals);
                //     settovalule(myValue);
                // }else{
                //     settovalule(transfer_value);
                // }
            //     setoptionlist(choices.split('|'))
            // });
            setShow(false);
        };
        setOnevote();
    }, [props.voteid, props.id]);

    const handleClicktoVote = async () => {

        dispatch({ type: 'LOADINGTYPE', payload: t('Voting') });
        props.handleClose()

        const signer = web3Instance.getSigner(Account)
    
        try {
            await api.vote.doVote(daoOrg, signer, voteid, selected)
            // setafterchoice(data);
            dispatch({ type: 'LOADINGTYPE', payload: null });
        } catch (error) {
            console.error(error)
            dispatch({ type: 'LOADINGTYPE', payload: null });
            dispatch({ type: 'MSGTYPE', payload: { msg: `Vote failed: ${(error.data && error.data.message) || 'unknown'}`, closable: true } });
        }
    }

    let { handleClose, showTips } = props;
    return (
        <div>

            <Modal visible={showTips} onCancel={handleClose} footer={null} maskClosable={false} centered={true}>
                <div className="title"><span>{t('vote.Vote')}</span></div>
                <SectionBrdr>
                    {
                        show && <LoadingNew />
                    }
                    {
                        !show && <ul>
                            <li>
                                <Titles>{t('vote.Title')}</Titles>
                                <div>{title}</div>
                            </li>
                            {
                                (voteData.isTransfer || voteData.isTransferManageFee) && <Li>
                                    <TransferLft>
                                        <Titles>{voteData.isTransfer ? t('vote.Transfer') : t('vote.ManageFeeTransfer')} </Titles>
                                        <Amount>{toValue} {symbol}</Amount>
                                    </TransferLft>
                                    <Addr>
                                        <Titles>{t('vote.Recipient')}</Titles>
                                        <div>{toaddress}</div>
                                    </Addr>

                                </Li>
                            }
                            {
                                voteData.isApprove && <Li>
                                    {
                                        approveInfo && <TransferLft>
                                            <Titles>{t('vote.ApproveTitle')}</Titles>
                                            <Amount>{approveInfo.amount} {approveInfo.symbol}</Amount>
                                        </TransferLft>
                                    }
                                    <Addr>
                                        <Titles>{t('vote.ApproveGp')}</Titles>
                                        <div>{toaddress}</div>
                                    </Addr>
                                </Li>
                            }
                            {
                                (voteData.isNewGp || voteData.isKick) && <Li>
                                    <Addr>
                                        <Titles>{voteData.isKick ? t('vote.Kick') : t('vote.AddGP')}</Titles>
                                        <div>{toaddress}</div>
                                        <GpListInfo gplist={gplist} />
                                        <VoteConfigInfo {...newVoteData} />
                                    </Addr>
                                </Li>
                            }
                            {
                                voteData.isPeriod && <Li>
                                    <Addr>

                                        <Titles>{t('vote.lockup')}</Titles>
                                    </Addr>
                                </Li>
                            }
                            <li>
                                <Titles>{t('vote.VoteDescription')}</Titles>
                                <Desc>{desc}</Desc>
                            </li>
                            <li className='voteSelect'>
                                <Titles>{t('vote.Select')}</Titles>
                                <div>
                                    <Radio.Group onChange={handleRadio} value={selected} buttonStyle="solid">
                                        {optionlist.map((i, index) => (

                                                <div className={parseInt(active) === index ? 'radioActive radioBrdr' : 'radioBrdr'} id={`active_${index}`} key={index}>
                                                <Radio value={index===0} disabled={disabledVote}>{i.split(":")[0]} </Radio>
                                            </div>

                                        ))
                                        }
                                    </Radio.Group>
                                </div>


                            </li>
                            <NextBrdr>
                                <Button type="primary" onClick={()=>handleClicktoVote()}
                                        disabled={disabledVote || selected===''}>{t('vote.Submit')}</Button>
                            </NextBrdr>
                        </ul>
                    }

                </SectionBrdr>
            </Modal>
        </div>
    )

}

