import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Button, Input } from 'antd';
import ConnectContract from './api/connectContract';
import { useWeb3 } from "./api/contracts";
import ApiInterface from "./api";
import error from './img/error.png';

const web3 = require('web3');

const Reg = styled.div`
    width: 87rem !important;
    .title{
        height: 4rem;
        font-size: 3.4rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        color: #10134E;
        line-height: 4rem;
        text-align: center;
        margin-top: 4rem;
    }
    .line{
        margin-top: 4rem;
        .label{
            height: 2.1rem;
            font-size: 1.8rem;
            font-family: HelveticaNeue;
            font-weight: 300;
            color: #10164B;
            line-height: 2.1rem;
            margin-bottom: 1rem;
            span{
                height: 1.4rem;
                font-size: 1.2rem;
                font-family: HelveticaNeue;
                font-weight: 300;
                color: #A6A6B7;
                line-height: 1.4rem;
                margin-left: 1rem;
            }
        }
        
    }
    .Btn{
        width: 18rem;
        height: 5rem;
        border-radius: 0.8rem;
        font-family: HelveticaNeue;
        &:disabled{
            opacity: 0.3;
        }
    }
`

const ErrorTips = styled.div`
    display: flex;
    font-size: 1.4rem;
    line-height: 2rem;
    height: 3rem;
    align-items: center;
    color: #E10000;
    /* padding: 0 2rem; */
    img{
        width: 1.4rem;
        margin-right: 1rem;
    }
`

const Registration = () => {
    const { state, dispatch } = useWeb3();
    const { maincontract, api } = state;

    const [isBind, setisBind] = useState(false);
    const [address, setAddress] = useState('');
    const [bindcontract, setbindcontract] = useState(null);
    const [showError, setshowError] = useState(false);

    const mainAddress = window.mainAddress;

    const getmaincontract = async () => {
        if(api == null) return ;
        let data = await ConnectContract(api, 'bindaddress', mainAddress.bindAddress);
        setbindcontract(data)
    }

    useEffect(async () => {
        dispatch({ type: 'LOADINGTYPE', payload: 'loading data'  });
        getmaincontract();
    }, []);


    useEffect(() => {
        if (bindcontract == null|| api==null) getmaincontract();
        const getEthAddress = async () => {
            await ApiInterface.bindAddress.isBind(bindcontract).then(data => {
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setisBind(data);
                if (!data) {

                    // setAddress(data);

                }
            })
        };
        getEthAddress();
    }, [bindcontract,api]);

    const handleAddress = (e) => {
        setAddress(e.target.value);
        if (e.target.value) {
            setisBind(false);
            if (web3.utils.isAddress(e.target.value)) {
                setshowError(false);
            } else {
                setshowError(true);
            }
        } else {
            setshowError(false);
        }
    }

    const submit = () => {
        const bind = async () => {
            await ApiInterface.bindAddress.bind(bindcontract, address,(data) => {
                if (data) {
                    dispatch({ type: 'LOADINGTYPE', payload: null });
                    dispatch({ type: 'MSGTYPE', payload: { msg: 'Binding success' } });
                }
            }).catch((error) => {
                dispatch({ type: 'LOADINGTYPE', payload: null });
                dispatch({ type: 'MSGTYPE', payload: { msg: 'Binding failure' } });
            });
        };
        dispatch({ type: 'LOADINGTYPE', payload:'loading data' });
        bind();
    }

    return (
        <Reg className="container">
            <div className="title">IDO Registration</div>

            <div className="line">
                <div className="label">
                    Ethereum Address
                    {
                        isBind && <span>Is Bind</span>
                    }
                </div>
                <div className="inputBrdr">
                    <Input type="text"
                        placeholder='Ethereum Address'
                        value={address}
                        onChange={handleAddress}
                    />
                </div>
            </div>

            {
                showError && <ErrorTips>
                    <img src={error} alt="" />
                    <div className='tipsDesc'>Address format error</div>
                </ErrorTips>
            }
            <div className="line" style={{ textAlign: 'center', marginTop: '10rem' }}>
                <Button className="Btn" type="primary" disabled={!address.length || showError} onClick={submit}>
                    Submit
                </Button>
            </div>
        </Reg>
    );
}

export default Registration;
