const Tips = styled.div`
    text-align: center;
    font-size: 1.8rem;
    font-family: HelveticaNeue;
    line-height: 2.5rem;
    color: #0069ff;
    cursor: pointer;
`;


const DaosModal = (props) => {
    const { moreDaos, handleClose } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { maincontract, Account, ChainID, daoType, certifiedDAOs } = state;
    const [imglist, setimglist] = useState([]);
    const [showCreatDaoBtn, setshowCreatDaoBtn] = useState(true);
    const { pathname } = useLocation()
    const allDaoRef = useRef()

    useEffect(() => {
        if (pathname === '/create') {
            setshowCreatDaoBtn(false)
        } else {
            setshowCreatDaoBtn(true)
        }
    }, [pathname])


    const handleClick = () => {
        if (Account != null) {
            props.history.push('/create');
            dispatch({ type: 'DAOTYPE', payload: null });
        }
    }

    const handleClicktoAbout = useCallback(
        (handleOrId) => {
            props.history.push(`/about/${handleOrId}`);
            dispatch({ type: 'DAOTYPE', payload: null });
        },
        []
    )
    const handleSearch = (e) => {
        let value = e.target.value || ''
        value = e.target.value.trim()
        if (allDaoRef && allDaoRef.current && allDaoRef.current.goSearch) {
            allDaoRef.current.goSearch(value)
        }
    }
    const switchModal = () => {
        dispatch({ type: 'DAOTYPE', payload: 'all' });
    }

    return (
        // <Animate animation="move-down">
        // <Animate animation="zoom-left"  showProp="show">
        <Modal
            visible={moreDaos}
            onCancel={handleClose}
            className='daoModal'
            footer={null}
            mask={false}
            transitionName="ant-move-down"
        >
            <DaoBody>
                <div className="top">

                    {
                        daoType === 'all' && <div className="left">
                            <img src={searchImg} alt="" />
                            <Inputbrdr type="text" placeholder="Search" onPressEnter={(e) => handleSearch(e)} />
                        </div>
                    }
                    {
                        daoType === 'my' && <div className="left mydao">{t('mydao.myDAO')}</div>
                    }

                    {showCreatDaoBtn &&
                        <Button type="primary" onClick={() => handleClick()} style={{ width: '20rem', height: '6rem' }}>
                            {t('mydao.Create')}
                        </Button>}
                </div>
                <div className="daos">
                    { daoType === 'my' && <MyDaoList handleClicktoAbout={handleClicktoAbout} /> }
                    { daoType === 'all' && <DaoTabList ref={allDaoRef} handleClicktoAbout={handleClicktoAbout} /> }

                    <div className="bottom">
                        {
                            daoType === 'my' && <Tips onClick={() => switchModal()}>
                               {t('mydao.Explorer')}
                            </Tips>
                        }
                    </div>
                </div>
            </DaoBody>
        </Modal>

    );
}

export default DaosModal;
