import React, {useCallback, useEffect, useState} from "react";
import {Button, Input, Modal,Tag} from "antd";
import styled from "styled-components";
import { ethers } from 'ethers'

import Arrow from '../../img/tianchongxing.png';
import Cart from '../../img/gouwuche.png';
import api from "../../api";
import { useWeb3 } from "../../api/contracts";
import { getShortDisplay } from "../../utils/publicJs";
import {useTranslation} from "react-i18next";

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      //max-height: 70rem;
      overflow-y: auto;
    }
    .label{
        min-height: 1.4rem;
    }
    .labelTips{
      font-size: 1.4rem;
    }
    .star{
      color: #ff6f5a;
      margin-left: 1.5rem;
    }
    .pr{
      position: relative;
      img{
          width: 4.4rem;
          position: absolute;
          top:-1.5rem;
          left: 50%;
          margin-left: -2.2rem;
      }
    }
    
`

const Title = styled.div`
    position: relative;
    font-size: 3.2rem;
    text-align: center;
    .tag {
        position: absolute;
        left: calc(50% + 8rem);
        top: 1rem;
    }
`

const NextBrdr = styled.div`
  .defaultBox{
    border-radius: 1.2rem!important;
    img{
      height: 1.8rem;
      width: 1.8rem;
      margin-right: 1rem;
    }

  }
`

const Box = styled('div')`
    background: #F4F4FA;
    border-radius: 1.2rem;
    .balance{
      white-space: nowrap;
      margin-right: 3rem;
        color: #A6A6B7;
    }
`

const PriceTips = styled('div')`
    margin-top: 2rem;
    font-size: 1.4rem;
    font-weight: 300;
    color: #D52473;
    line-height: 1.6rem;
`

const s = 0.5

export default function TokenBuy(props) {
    const { show ,closeTokenBuy, fund } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg, vaultcontract, daoContractAddress } = state;
    let { t } = useTranslation();
    const [tokenInAddress, setTokenInAddress] = useState(fund.address)
    const [tokenOutAddress, setTokenOutAddress] = useState()
    const [fundAmount, setFundAmount] = useState()
    const [tokenAmount, setTokenAmount] = useState()
    const [tokenData, setTokenData] = useState({})
    const [tokenInData, setTokenInData] = useState({})
    const [swapNum, setSwapNum] = useState(0)

    const handleInputChange = async(address) =>{
        setTokenOutAddress(address)
        if (ethers.utils.isAddress(address)) {
            try {
                const contract = await api.erc20.getContract(web3Instance, address)
                const symbol = await contract.symbol()
                const deci = await contract.decimals()
                const balance = ethers.utils.formatUnits(await contract.balanceOf(vaultcontract.address), deci)

                setTokenData({
                    symbol, balance, balanceDisplay: getShortDisplay(balance), deci
                })
                if (tokenInAddress && ethers.utils.isAddress(tokenInAddress)) {
                    try {
                        const num = await api.venture.getAmountOutMin(web3Instance, web3Instance.getSigner(Account), {
                            tokenIn: tokenInAddress,
                            tokenOut: address,
                            amountIn: ethers.utils.parseUnits('1', tokenInData.deci)
                        })
                        setSwapNum(getShortDisplay(ethers.utils.formatUnits(num, deci)))
                    } catch (error) {
                        console.error('getAmountOutMin failed', error)
                    }
                    
                }
            } catch (error) {
                console.error('handleInputChange failed:', error)
            }
        }
    }

    const handleChangeTokenInAddress = async(address)=>{
        setTokenInAddress(address)
        if (ethers.utils.isAddress(address)) {
            try {
                const contract = await api.erc20.getContract(web3Instance, address)
                const symbol = await contract.symbol()
                const deci = await contract.decimals()
                const balance = ethers.utils.formatUnits(await contract.balanceOf(vaultcontract.address), deci)
                setTokenInData({symbol, deci, balance, balanceDisplay: getShortDisplay(balance)})

                if (tokenOutAddress && ethers.utils.isAddress(tokenOutAddress)) {
                    const num = await api.venture.getAmountOutMin(web3Instance, web3Instance.getSigner(Account), {
                        tokenIn: address,
                        tokenOut: tokenOutAddress,
                        amountIn: ethers.utils.parseUnits('1', deci)
                    })
                    setSwapNum(getShortDisplay(ethers.utils.formatUnits(num, deci)))
                }
            } catch (error) {
                console.error('handleChangeTokenInAddress failed:', error)
            }
        }
    }

    useEffect(()=>{
        if (!fundAmount && tokenAmount) setTokenAmount(0)
        if (!swapNum || !fundAmount) return
        setTokenAmount(Number(swapNum)*(1-s/100)*fundAmount)
    }, [swapNum, fundAmount])


    const handleClose = () => {
        closeTokenBuy()
    }

    const clickBuyToken = async() => {
        if (!fundAmount || !tokenAmount || !tokenInAddress || !tokenOutAddress) return
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.buyingtoken')}` });
            const signer = web3Instance.getSigner(Account)
            await api.venture.buyToken(daoOrg, signer, {
                tokenIn: tokenInAddress,
                tokenOut: tokenOutAddress,
                amountIn: ethers.utils.parseUnits(String(fundAmount), tokenInData.deci),
                amountOut: ethers.utils.parseUnits(String(tokenAmount * (1-s/100)), tokenData.deci),
            })
            
        } catch (error) {
            console.error(error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.buyFailed')}: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    const getFundBalance = async()=>{
        if (!tokenInAddress || !vaultcontract) return
        if (!ethers.utils.isAddress(tokenInAddress)) return
        const balance = await getLimitInfo()
        setTokenInData({ ...tokenInData, balanceDisplay: getShortDisplay(ethers.utils.formatUnits(balance, tokenInData.deci)) })
        
    }

    useEffect(() => {
        if (!show) return
        getFundBalance()
    }, [tokenInAddress, vaultcontract, show])


    useEffect(async ()=>{
        await setTokenInData({symbol:fund.symbol, deci: fund.deci})
        await setTokenInAddress(fund.address)
    }, [props.fund])

    const getLimitInfo = useCallback(
        async () => { 
            try {
                const contract = await api.vote.getGrantContract(web3Instance, daoContractAddress["GrantMethodManager"])
                const result = await contract.getTokenLimit(tokenInAddress, Account)
                return result[1].sub(result[0])
            } catch (error) {
                console.error('getTokenLimit failed', error)
                return ethers.BigNumber.from(0)
            }
            
        },
        [daoContractAddress, Account, tokenInAddress]
    )
    
    return <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
        <Title className="font-light">
            <span>{t('venture.TokenBuy')}</span>
            <Tag className="tag" color="#2db7f5">{t('venture.pancake')}</Tag>
        </Title>
        <div>
            <div className="label">{t('venture.TokenAddress')}</div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('venture.enter')}
                    name='pairAddress'
                    value={tokenInAddress}
                    onChange={(e)=>handleChangeTokenInAddress(e.target.value)}
                    autoComplete="off"
                />
            </div>
            <div className="label">{t('venture.outAddress')}</div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('venture.enter')}
                    value={tokenOutAddress}
                    autoComplete="off"
                    onChange={(e)=>handleInputChange(e.target.value)}
                />
            </div>
           <div className="label">{tokenInData && tokenInData.symbol}</div>
            <Box>
                <div className="inputBrdr">
                    <Input
                        placeholder={t('venture.enter')}
                        name='fundAmount'
                        value={fundAmount}
                        autoComplete="off"
                        type="number"
                        onChange={(e)=>setFundAmount(e.target.value)}
                    />
                    <div className="balance">{t('venture.Balance')}: {tokenInData.balanceDisplay} {tokenInData && tokenInData.symbol}</div>
                </div>
            </Box>
            <div className="label pr">{tokenData.symbol} <img src={Arrow} alt=""/></div>
            <Box>
                <div className="inputBrdr">
                    <Input
                        // placeholder={t('venture.enter')}
                        name='tokenAmount'
                        value={tokenAmount}
                        autoComplete="off"
                        type="number"
                        onChange={(e)=>setTokenAmount(Number(e.target.value))}
                    />
                    { tokenData.symbol && <div className="balance">{t('venture.Balance')}: {tokenData.balanceDisplay} {tokenData.symbol}</div> }
                    

                </div>
            </Box>


            <PriceTips>
                {tokenData.symbol && <span>{t('venture.Price')}: {swapNum} {tokenData.symbol} per {fund.symbol}</span>}
            </PriceTips>
            <PriceTips>{t('venture.tolerance')}: {s}%</PriceTips>
            <NextBrdr >
                <Button 
                    type="primary" 
                    className="defaultBox" style={{ width: '100%', marginTop: '3rem' }} 
                    disabled={tokenAmount < 0 || fundAmount < 0}
                    onClick={clickBuyToken}
                >
                    <img src={Cart} alt=""/>{t('venture.BuyNow')}
                </Button>
            </NextBrdr>
            </div>


    </ModalBox>
}
