import React, { useEffect, useState } from 'react';
import { Button, Modal, Input } from 'antd';

import api from "../../api";
import { useWeb3 } from "../../api/contracts";

import { useTranslation } from "react-i18next";
import remove from "../../img/shutdown.png";
import add from "../../img/Add.png";

export default function AddApply(props) {

    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, daoOrg } = state;

    const [name, setname] = useState('');

    const [addMember, setaddMember] = useState(false);

    let { t } = useTranslation();
    let { handleClose, showTips } = props;

    // useEffect(() => {
    //     if (addMember) {
    //         dispatch({ type: 'LOADINGTYPE', payload: null });
    //         props.handleTips()
    //     }
    // }, [addMember])

    const handleInputChange = (e) => {
        const { value } = e.target;
        setname(value)
    }

    const handleSubmit = async () => {
        dispatch({ type: 'LOADINGTYPE', payload: t('apply.ApplyMember') });
        handleClose()
        try {
            const signer = web3Instance.getSigner(Account)
            await api.org.joinDao(daoOrg, signer, {
                name: name,
                address: Account
            })
            // TODO check if dao is config join directly
            props.refresh();
            setname('');
        } catch (error) {
            console.error("[joinDao] failed", error)
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('apply.ApplyMember') }}: ${error.data.message}` } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    return <div>
        <Modal visible={showTips} onCancel={handleClose} footer={null}>
            <div className="title">
                {/* <img src={applyList} alt="" /> */}
                <span>Apply</span>
            </div>

            <div className="label">{t('apply.Name')}</div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('apply.Name')}
                    name='name'
                    value={name}
                    autoComplete="off"
                    onChange={handleInputChange}
                />
            </div>

            <Button type="primary" onClick={() => handleSubmit()} style={{ width: '100%', marginTop: '3rem' }}>
                {t('apply.Submit')}
            </Button>
        </Modal>
    </div>;
}
