import { useWeb3 } from "../api/contracts";


const Msg = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    .loagding{
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        .logo{
            width: 3rem;
            height: 5rem;
            position: relative;
            margin-right: 0.9rem;
        }
        .logo::before{
            background: url(${logoShadow});
            background-size: 100%;
            content: '';
            width: 3rem;
            height: 1rem;
            position: absolute;
            top: 4rem;
            left: 0;
            animation: shadow .5s linear infinite;
        }
        .logo::after{
            background: url(${logoRound});
            background-size: 100%;
            animation: rotate 0.5s linear infinite;
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 3rem;
            height: 3rem;
        }
        @keyframes shadow {
            0%, 100% {transform: scaleX(1);}
            50% {transform: scaleX(1.2);}
        }
        @keyframes rotate {
            0% {
                transform: translateY(0) ;
            }
            25% {
                transform: translateY(0.5rem);
            }
            50% {
                transform: translateY(1.5rem) scale(1.1, 0.9);
            }
            75% {
                ransform: translateY(0.5rem) ;
            }
            100% {
                transform: translateY(0) ;
            }
        }
        .text{
            font-size: 1.4rem;
            font-family: HelveticaNeue;
            font-weight: 400;
            color: rgba(1, 6, 67, 0.6);
            line-height: 3rem;
        }
    }
    .msg{
        font-size: 1.8rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        color: #010643;
        line-height: 2.1rem;
        padding-top: 1.5rem;
    }
`


const LoadingModal = (props) => {
    const { showMsg, handleClose } = props;
    const { state, dispatch } = useWeb3();
    const { loadingType } = state;

    return (
        <Modal
            closable={false}
            visible={showMsg}
            onCancel={handleClose}
            maskClosable={false}
            footer={null}
            centered={true}
        >
            <Msg>
                <div className="loagding">
                    <div className="logo" />
                    <div className="text">Loading…</div>
                </div>
                <div className="msg">
                    {loadingType}
                </div>
            </Msg>
        </Modal>
    );
}

export default withRouter(LoadingModal);
