import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Modal, Input } from 'antd';

import {ethers} from 'ethers';
import { useWeb3 } from "../../api/contracts";
import { useTranslation } from "react-i18next";

const AddTokenModel = forwardRef((props, ref) => {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { web3Instance, daoOrg, Account} = state;
    const [tokenAddress, setTokenAddress] = useState('');


    const handleChange = (e) => {
        const { value } = e.target;
        setTokenAddress(value)
    }

    const clearSelected = () =>{
        setTokenAddress('')
        dispatch({ type: 'LOADINGTYPE', payload: null });
        dispatch({ type: 'BALANCE', payload: true });
        // props.setShow();
    }

    const CloseClear = () =>{
        setTokenAddress('')
        props.handleClose()
    }

    const handleConfirm = async () => {
        console.log(window.mainAddress.actionVaultOptToken)
        if (!tokenAddress) return

        props.handleClose()

        dispatch({ type: 'LOADINGTYPE', payload: 'Add Token' });

        const templateAction = ethers.utils.defaultAbiCoder.encode(
            ['address', 'bytes'],
            [
                window.mainAddress.actionVaultOptToken,
                ethers.utils.defaultAbiCoder.encode(
                    ['address', 'bool'],
                    [tokenAddress, true]
                )
            ]
        );
        console.log('* templateAction', templateAction)
        const signer = web3Instance.getSigner(Account)
        try {
            const res =  await daoOrg.connect(signer).action(templateAction);
            await res.wait();
        } catch (error) {
            console.error(error)
        }
        clearSelected();
    }
   

    useImperativeHandle(ref, () => ({
        resultToVault: () => {
            return {
                tokenAddress
            }
        },
    }));

    let visible = props.visible;
    return (
        <div>
            <Modal visible={visible} onCancel={CloseClear} footer={null}>
                <div className="title">
                    <span>Add Token</span>
                </div>

                <div className="label">Token Address</div>
                <div className="inputBrdr">
                    <Input
                        placeholder="Token Address"
                        value={tokenAddress}
                        name='tokenAddress'
                        onChange={handleChange}
                    />
                </div>

                <Button type="primary" onClick={() => handleConfirm()} style={{ width: '100%', marginTop: '3.9rem' }}>Confirm</Button>
            </Modal>
        </div>
    )
})
export default AddTokenModel
