import { useState, forwardRef, useImperativeHandle, useCallback, useEffect } from 'react';
import { Upload } from 'antd'
import { LoadingOutlined } from '@ant-design/icons';
import { uploadImg, getGateway } from '../../api/apiHTTP'
import styled from "styled-components";
import addproject from "../../img/addproject.png"
import { useWeb3 } from "../../api/contracts";
import { useTranslation} from 'react-i18next';

const LOGO = styled(Upload)`
    width: 12rem;
    height: 12rem;
    background: #F4F4FA;
    border-radius: 1.2rem;
    position: relative;
    text-align: center;
    margin-right: 1rem;
    .ant-upload.ant-upload-select-picture-card {
        width: 100%;
        height: 100%;
        background: #fff;
    }
    .occupy-img {
        width: 3rem;
        height: 3rem;
        margin-top: 3rem;
    }
    .occupy-text {
        font-size: 1.8rem;
        color: #A6A6B7;
        margin-top: 1rem;
    }
    .mask {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        cursor: pointer;
        
    }
    .image-item {
        width: 100%;
        height: 100%;
    }
    .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -9px;
        margin-left: -9px;
    }
`


const ImageUploader = forwardRef((props, ref)=>{
    const { t } = useTranslation();
    const { dispatch } = useWeb3();

    const [loading, setLoading] = useState(false)
    const [imageUrl, setImageUrl] = useState(props.imgCode)
    const [imgCode, setImgCode] = useState(null)

    const showImg = useCallback(async(code)=>{
        if (!code) return
        const gateway = await getGateway()
        setImageUrl(gateway+code)
    }, [])

    useEffect(()=>{
        setImgCode(props.imgCode)
        showImg(props.imgCode)
    }, [props.imgCode])

    const getFileData = async (file) => {
        const reader = new FileReader();
        reader.addEventListener('load', async () => {
            setImageUrl(reader.result)
        });
        reader.readAsDataURL(file);
    }
    const checkFile = useCallback((file)=>{
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('Upload.ErrorType'), closable: true } });
            return false
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('Upload.ErrorSize'), closable: true } });
            return false
        }
        return true
    }, [])

    const beforeUpload = file => {
        goUpload(file)
        return false
    }

    const goUpload = async (file)=>{
        if (!checkFile(file)) return
        dispatch({ type: 'LOADINGTYPE', payload: t('create.UploadLogo') });
        setLoading(true)
        try {
            const result = await uploadImg(file)
            setImgCode(result.data.data.fileUpload)
            props.updateUrl(result.data.data.fileUpload)
            getFileData(file)
        } catch (error) {
            console.error("Upload logo failed: ", error)
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('create.UploadLogoFailed')}`, closable: true } });
        } finally {
            setLoading(false)
            dispatch({ type: 'LOADINGTYPE', payload: null});
        }
    }

    useImperativeHandle(ref, () => ({
        upload: goUpload,
        imgCode: imgCode,
        clear: ()=>{
            setImageUrl(null)
            setImgCode(null)
        }
    }));


    return (
        <LOGO
        name="logo"
        listType="picture-card"
        className="logo-uploader"
        showUploadList={false}
        beforeUpload={beforeUpload}
        // onChange={handleChange}
        >
            <div className='mask'>
                {
                    loading && <LoadingOutlined className='loading' /> 
                }
                { !loading && !imageUrl && <>
                    <img className='occupy-img' src={addproject} alt="" />
                    <div className='occupy-text font-light'>Upload</div>
                </> }
                
            </div>
            {
                imageUrl && <img className="image-item" src={imageUrl} alt="" width="100" />
            }
        </LOGO>
    )
})

export default ImageUploader