import React, {useEffect, useState} from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";
import { useWeb3 } from "../../api/contracts";
import api from "../../api";
import { ethers } from "ethers"
import { getShortDisplay } from "../../utils/publicJs";
import {useTranslation} from "react-i18next";


const ModalBox = styled(Modal)`
    &.ant-modal{
      width: 78.6rem!important;
    }
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     //font-family: "Roboto-Light"!important;
    }
    .labelTips{
      font-size: 1.4rem;
    }
    .suffix {
        color: #A6A6B7 !important;
        font-size: 1.4rem !important;
    }
 
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
  button{
    color: #D52473;
    border-color: #D52473;
    padding: 1rem 0;
    height: 7.6rem;
  }
`

const MaxButton = styled.div`
  cursor: pointer;
`

const Ulbox = styled('div')`
    display: flex;
    flex-wrap: wrap;
    li{
      width: 50%;
      margin-bottom: 3rem;
      //font-family: "Roboto-Light"!important;
      padding-left: 1.5rem;
      box-sizing: border-box;
      &:nth-child(1){
        border-left:0.6rem solid #FFDD42;
      } 
      &:nth-child(2){
        border-left:0.6rem solid #004EFF;
      }
      &:nth-child(3){
        border-left:0.6rem solid #ED520D;
      } 
      &:nth-child(4){
        border-left:0.6rem solid #00C29F;
      }
    }
    .titleTopF{
      color: #10164B;

      font-size: 1.8rem;
      opacity: 0.6;
    }
    .contentNum{
        font-weight: 300;
        color: #10164B;
        font-size: 2.2rem;
    }
`



export default function Withdraw(props) {
    const { show ,closeWithdraw, mintToken, fund } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg, venturecontract } = state;

    const [claimed, setClaimed] = useState(0)
    const [avaluable, setAvaluable] = useState(0)
    const [shareToken, setShareToken] = useState(0)
    const [gpBonus, setGpBonus] = useState(0)
    const [amount, setAmount] = useState()

    const handleClose = () =>{
        closeWithdraw()
    }

    const doWithdraw = async()=>{
      handleClose()
      try {
          dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.withdrawing')}` });
          const signer = web3Instance.getSigner(Account)
          let wamount
          if (amount === avaluable) {
            wamount = 0
          } else {
            wamount = ethers.utils.parseUnits(String(amount), fund.deci)
          }
          await api.venture.withdraw(daoOrg, signer, wamount)
          setAmount(null)
          props.refresh && props.refresh()
      } catch (error) {
          console.error('withdraw failed', error)
          const msg = (error.data && error.data.message) || 'failed'
          dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.withdrawFailed')}: ${msg}`, closable: true } });
      } finally {
          dispatch({ type: 'LOADINGTYPE', payload: null });
      }
    }

    useEffect(()=>{
      if (!venturecontract || !Account || !show) return
      const getClaimed = async()=>{
        const res = await venturecontract.getClaimedAmount(Account)
        setClaimed(ethers.utils.formatUnits(res[1].add(res[2]), mintToken.deci))
      }
      getClaimed()
    }, [venturecontract, show])

    useEffect(()=>{
      if (!daoOrg || !Account || !show) return
      const getActionContract = async()=>{
          try {
            const templateConfigAddress = await daoOrg.templateConfig()
            const signer = web3Instance.getSigner(Account)
            const res = await api.venture.getClaimEarningEstimate(web3Instance, signer, {
              account: Account,
              template: templateConfigAddress,
              amountClaim: claimed
            })
            setAvaluable(ethers.utils.formatUnits(res[0], fund.deci))
            setShareToken(ethers.utils.formatUnits(res[1], mintToken.deci))
            setGpBonus(ethers.utils.formatUnits(res[2], fund.deci))
          } catch (error) {
            console.error('getClaimEarningEstimate failed', error)
          }
      }
      getActionContract()
    }, [daoOrg, Account, show])
    

    return <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >{t('venture.Withdraw')}</span>
        </div>
        <div>
            <Ulbox>
                <li>
                    <div className="titleTopF">{t('venture.share')}</div>
                    <div className="contentNum">{getShortDisplay(shareToken)} {mintToken.symbol}</div>
                </li>
                <li>
                    <div className="titleTopF">{t('venture.YourGPBonus')}</div>
                    <div className="contentNum">{getShortDisplay(gpBonus)} {fund.symbol}</div>
                </li>
                <li>
                    <div className="titleTopF">{t('venture.Claimed')}</div>
                    <div className="contentNum">{getShortDisplay(claimed)} {fund.symbol}</div>
                </li>
                <li>
                    <div className="titleTopF">{t('venture.Avaluable')}</div>
                    <div className="contentNum">{getShortDisplay(avaluable)} {fund.symbol} </div>
                </li>
            </Ulbox>
            <div className="label">Withdraw amount </div>
            <div className="inputBrdr">
                <Input
                    placeholder={t('venture.enter')}
                    value={amount}
                    autoComplete="off"
                    type="number"
                    onChange={(e)=>setAmount(Number(e.target.value))}
                />
                <MaxButton className='suffix' onClick={()=>{setAmount(avaluable)}}>Max: {getShortDisplay(avaluable)}</MaxButton>
            </div>

            <NextBrdr >
                <Button onClick={doWithdraw} disabled={!amount || amount>avaluable} style={{ width: '100%', marginTop: '3rem' }}>
                    {t('venture.Withdraw')}
                </Button>
            </NextBrdr>
        </div>


    </ModalBox>
}
