            if (!v.toAddress) v.toAddress = v.vaultContractAddress
            const tokenContract = await api.erc20.getContract(web3Instance, v.tokenAddress)
            v.tokenName = await tokenContract.symbol()
            v.amount = ethers.utils.formatUnits(v.amount, await tokenContract.decimals())
