export default function FirstStep(props) {
    const { state, dispatch } = useWeb3();
    const { web3Instance, maincontract, Account } = state
    let { t } = useTranslation();

    const [imgUrl, setimgUrl] = useState('');
    const [errTips, seterrTips] = useState(false);
    const [showDisable, setshowDisable] = useState(true);
    const [name, setName] = useState('');
    const [twitter, setTwitter] = useState('');
    const [discord, setDiscord] = useState('');
    const [description, setdescription] = useState('');
    const [daohandle, setDaohandle] = useState('')

    const logoRef = useRef()

    useEffect(() => {
        if (!maincontract) {
            dispatch({ type: 'LOAD_MAINCONTRACT' });
        }
    }, [])

    const handleChange = (e) => {
        const { name, value } = e.target;

        if (name === "name") {
            setName(value)
        } else if (name === "twitter") {
            setTwitter(value)
        } else {
            setdescription(value)
        }
    }

    useEffect(() => {
        const form1 = JSON.parse(sessionStorage.getItem('firstStep'));
        // const imgUrl1 = JSON.parse(sessionStorage.getItem('imgUrl'));
        if (form1 != null) {
            setName(form1.name);
            setdescription(form1.description);
            setimgUrl(form1.imgUrl);
            setDaohandle(form1.daohandle || '')
            if (form1.socialUrls) {
                const urls = form1.socialUrls.split(';')
                setTwitter(urls[0])
                setDiscord(urls[1])
            }
        }

    }, []);

    const nextButtonDisable = useMemo(() => {
        return !name.trim().length || !description.length || !daohandle.trim().length
    }, [name, description, daohandle])

    const checkDaoHandle = useCallback(
        (contract, handle) => {
            return contract.isAliasExists(handle)
        },
        []
    )

    const toSecondStep = async () => {
        // check daohandle unique
        try {
            const isExists = await checkDaoHandle(maincontract, daohandle)
            if (isExists) {
                dispatch({ type: 'MSGTYPE', payload: { msg: `${t('create.DAOHandleError')}`, closable: true } });
                return
            }
        } catch (error) {

        }
        const data = {
            name,
            description,
            imgUrl,
            daohandle
        }
        if (twitter || discord) {
            data.socialUrls = [twitter, discord].join(';')
        }
        sessionStorage.setItem('firstStep', JSON.stringify(data))

        if (name && description && daohandle) {
            props.handlerSet(2);
        } else {
            seterrTips(true)
        }
    }
   
    return <div className="content">
        <Modal
            visible={errTips}
            onCancel={() => seterrTips(false)}
            className='newVoteBrdr homebtm'
            footer={null}
        >
            <Tip>
                {t('create.FillTips')}
            </Tip>
        </Modal>
        <StepContentArea>
            <Info>
                <div className="title">{t('create.Basicinformation')}</div>
                {/* <div className="detail mt1">
                    DAOWallet Will Soon Be Launched On Chrome Web Store. It Will Gradually Support Most Types .
                </div> */}
            </Info>
            <Content>
                {
                    <div className='uploadBrdr'>
                        <div className='title'>{t('create.DAOImage')}</div>
                        <LogoUpload ref={logoRef} imgCode={imgUrl} updateUrl={url => setimgUrl(url)} />
                    </div>
                }
                <div className="line">
                    <div className="title"><span>*</span>{t('create.DAOName')}</div>
                    <div className='inputBrdr input-line'>
                        <Input
                            placeholder={t('create.FillName')}
                            value={name}
                            name='name'
                            autoComplete="off"
                            maxLength="60"
                            onChange={handleChange}
                        />

                    </div>
                </div>
                <div className="line">
                    <div className="title"><span>*</span>{t('create.DAOHandle')}</div>
                    <div className='inputBrdr input-line'>
                        <Input
                            placeholder={t('create.DAOHandleTip')}
                            value={daohandle}
                            name='daohandle'
                            autoComplete="off"
                            maxLength="60"
                            onChange={e => setDaohandle(e.target.value)}
                        />
                    </div>
                </div>
                <div className="line">
                    <div className="title">{t('create.twitter')}</div>
                    <div className='inputBrdr input-line'>
                        <Input
                            placeholder={t('create.twitterTips')}
                            prefix="@"
                            value={twitter}
                            name='twitter'
                            autoComplete="off"
                            onChange={handleChange}
                        />
                    </div>
                </div>
                <div className="line">
                    <div className="title">{t('create.discord')}</div>
                    <div className='inputBrdr input-line'>
                        <Input
                            placeholder={t('create.discordTip')}
                            value={discord}
                            name='discord'
                            autoComplete="off"
                            onChange={e => setDiscord(e.target.value)}
                        />
                    </div>
                </div>

                <div className="line">
                    <div className="title"><span>*</span>{t('create.DAODescription')}</div>
                    <div className='inputBrdr textareaB input-line'>
                        <Input.TextArea
                            placeholder={t('create.FillDAODescription')}
                            value={description}
                            name='description'
                            autoComplete="off"
                            onChange={handleChange}
                            allowClear={true}
                        />
                    </div>
                </div>

            </Content>
        </StepContentArea>
        <StepFooter>
            <Button className="nextBtn" type="primary" disabled={nextButtonDisable} onClick={toSecondStep}>
                {t('create.Next')}
                <img src={right} alt="" />
            </Button>
        </StepFooter>

    </div>

}
