import React, { useEffect, useState, useRef, useMemo, useCallback } from 'react';
import { useWeb3 } from "../../api/contracts";
import api from "../../api";
import { Trans, Translation, useTranslation } from 'react-i18next';
import { Modal, Button, Input, InputNumber, Select, DatePicker } from 'antd';
import { ethers } from 'ethers'
import moment from 'moment';
import styled from 'styled-components';
import right from '../../img/right.png';
import left from "../../img/left.png";
import remove from "../../img/remove.png";
import WranImg from "../../img/warn.png"
import addIcon from "../../img/addGP.png"
import VentureVoteConfig from './ventureVoteConfig';
import useNetwork from '../../hooks/useNetwork'
import PreviewModal from './preview/preview'
import { StepContentArea, StepFooter, VentureSubTitle, VentureSubDesc } from './style'

