import React, { useEffect, useState, useCallback } from 'react'
import { useWeb3 } from "../api/contracts";
import { getGateway } from "../api/apiHTTP"

const useGateway = () => {
    const { state } = useWeb3();
    const { ChainID } = state;

    const [gateway, setGateway] = useState()

    const reqGateway = useCallback(
        async () => {
            try {
                return await getGateway()
            } catch (error) {
                console.error('get gateway failed', error)
                return ''
            }
        },
        []
    )

    useEffect(
        async () => {
            setGateway(await reqGateway())
        },
        [ChainID]
    )

    return gateway
}

export default useGateway