import { Button, Modal } from 'antd';
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import api from '../../api';
import { useWeb3 } from "../../api/contracts";

const ModalBox = styled(Modal)`
     .defaultBtn{
        font-size: 1.8rem !important;
         //font-family: "Roboto-Regular";
      }
      .confirmInnerTitle{
      margin-bottom: 4rem;
      }
`

export default function ConfirmBox(props) {

    let { t } = useTranslation();
    let { handleClose, show, tips } = props;

    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg } = state;

    const tipsList = [
        "",
        "Close Venture",
        "Refund",
        "Freeze"
    ]

    const closeVenture = async()=>{
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.Closing')}` });
            const signer = web3Instance.getSigner(Account)
            await api.venture.closeVenture(daoOrg, signer)
            props.getVenturePeriod()
        } catch (error) {
            console.error('close failed', error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `close: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    const refund = async()=>{
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload:`${t('venture.Refunding')}` });
            const signer = web3Instance.getSigner(Account)
            await api.venture.refund(daoOrg, signer)
        } catch (error) {
            console.error('close failed', error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.Refunding')}: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    const freeze = async()=>{
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.Freezing')}` });
            const signer = web3Instance.getSigner(Account)
            await api.venture.freeze(daoOrg, signer)
            props.getVenturePeriod()
        } catch (error) {
            console.error('close failed', error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.Freezing')}: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }

    const handleConfirm = async()=>{
        switch (tips) {
            case 1:
                closeVenture()
                break
            case 2:
                refund()
                break
            case 3:
                freeze()
                break
            default:
                return
        }
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null} centered={true} maskClosable={false}>
        <div className="confirmInnerTitle">{tipsList[tips]}</div>
        <div className="confirmInnerBtnGroup">
            <Button className="defaultBtn" onClick={()=>handleClose()} >
             {t('venture.Cancel')}
            </Button>
            <Button className="defaultBtn" type="primary" onClick={()=>handleConfirm()}>
                {t('venture.Confirm')}
            </Button>

        </div>

    </ModalBox>;
}


