export const GpListInfo = (props)=>{
    const { t } = useTranslation();

    const { gplist } = props
    if (!gplist) return <></>

    return (
        <>
            <Titles className='next-title'>{t('vote.GpInfo')}</Titles>
            <GpInfo>
                {
                    gplist.map((item, i)=>{
                        return <li key={i} className="line">
                                    <div className="lft"> 
                                        <span className="text">{t('vote.GPInfoAddress')}</span>
                                        <span>{publicJs.AddresstoShow(item.address)}</span>
                                        <CopyStr className="btn-copy" address={item.address} />
                                    </div>
                                    <div className='inputBrdr rht'>
                                        <div className="text">{t('vote.bonus')}</div>
                                        <span>{item.bonus}%</span>
                                    </div>
                                </li>
                    })

                }
            </GpInfo>
        </>
    )
}

export default function VoteOverview(props) {
    const { state } = useWeb3();
    const { votecontract, vaultcontract, web3Instance, daoContractAddress } = state;
    const [voteid, setvoteid] = useState(null);
    const [desc, setdesc] = useState('');
    const [support, setsupport] = useState('');
    const [supportPercent, setsupportPercent] = useState('');
    const [toaddress, settoaddress] = useState('');
    const [optionlist, setoptionlist] = useState([]);
    const [toValue, settovalule] = useState('');
    const [endtime, setendtime] = useState('');
    const [executed, setexecuted] = useState('');
    const [need, setneed] = useState('');
    const [erc20_symbol, seterc20_symbol] = useState('');
    const [statusS, setStatusS] = useState('0');

    const [voteData, setVoteData] = useState({})
    const [newVoteData, setNewVoteData] = useState({})

    const [approveInfo, setApproveInfo] = useState()

    const [gplist, setGplist] = useState()


    let { t } = useTranslation();

    const handleClicktoVote = () => {
        props.history.push(`/vote/${props.match.params.handle}`)
    }
    useEffect(() => {
        if (!daoContractAddress) return
        const setOneVote = async () => {
            const vote_id = parseInt(props.match.params.voteid)
            try {
                const vdata = await api.vote.getVote(votecontract, vote_id)
                setendtime(vdata.endDisplay)
                setvoteid(vote_id);
                if (vdata.isExecuted) vdata.status = 1
                try {
                    if (!vdata.isExecuted && !vdata.isOpen) {
                        const canExecute =  (await votecontract.canExecute(vote_id)).toString()
                        if (canExecute === '0') {
                            vdata.status = 2    // not trigger
                        } else {
                            vdata.status = 0
                        }
                    }
                } catch (error) {
                    console.error("canExecute failed", error)
                }
                
                setoptionlist([
                    {
                        name: 'agree',
                        value: vdata.yea
                    },
                    {
                        name: 'disagree',
                        value: vdata.nay
                    }
                ])

                setVoteData(vdata)

                setsupport(vdata.minRequire);
                setsupportPercent(vdata.supportRequiredPct);


            } catch (error) {
            }
            // await api.vote.queryOneVote(votecontract, props.match.params.voteid).then(data => {
            //     if (!data) return;
            //     const {
            //         vote_id, title, desc, support_require_num, min_require_num, choices, to_address, transfer_value, start_date, vote_time, executed, need_trigger,erc20_symbol,status,erc20_address
            //     } = data;

            //     setendtime(PublicJS.formatvoteDateTime(start_date, vote_time));

            //     setneed(need_trigger);
            //     setStatusS(status)
            //     seterc20_symbol(erc20_symbol);
            //     settitle(title);
            //     setexecuted(executed);
            //     setvoteid(vote_id);
            //     setdesc(desc);
            //     settoaddress(to_address);

            //     if(native_token === erc20_address){
            //         let myNum = transfer_value.replace(/,/g,'');
            //         let myValue = parseInt(myNum) / Math.pow(10, decimals);
            //         settovalule(myValue);
            //     }else{
            //         settovalule(transfer_value);
            //     }

            //     setsupport(support_require_num);
            //     setmin(min_require_num);
            //     let arr = [], afterArr = [];
            //     arr = choices.split('|');
            //     arr.map((i, index) => {
            //         let obj = i.split(":");
            //         afterArr[index] = {
            //             value: obj[1] ? parseInt(obj[1]) : 0,
            //             name: obj[0]
            //         };
            //         return i;
            //     });
            //     if (afterArr.length) {
            //         setoptionlist(afterArr);
            //     }

            // });
        };

        setOneVote();
    }, [daoContractAddress]);

    useEffect(()=>{
        const getVoteMore = async()=>{
            if (!voteData) return
            if (voteData.isTransfer && !vaultcontract) return
            if (!voteData.isTransfer && !daoContractAddress) return
            try {
                if (voteData.isTransfer) {
                    const wdata = await api.vault.getTransferDetail(vaultcontract, voteData.wid)
                    settoaddress(wdata.proposer)
                    const erc20 = await api.erc20.getContract(web3Instance, wdata.token)
                    seterc20_symbol(await erc20.symbol())
                    settovalule(ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals()))
                } else if (voteData.isApprove) {
                    const contract = await api.vote.getGrantContract(web3Instance, daoContractAddress["GrantMethodManager"])
                    const wdata = await contract.registerVotes(voteData.wid)
                    settoaddress(wdata.gp)
                    settovalule(wdata.expire.toString())
                    if (wdata.extra) {
                        const result = ethers.utils.defaultAbiCoder.decode(
                            ["address", "address", "uint256"],
                            wdata.extra
                        )

                        try {
                            const erc20 = await api.erc20.getContract(web3Instance, result[0])
                            const symbol = await erc20.symbol()
                            const deci = await erc20.decimals()
                            setApproveInfo({
                                symbol: symbol,
                                amount: ethers.utils.formatUnits(result[2], deci)
                            })
                        } catch (error) {
                            console.error(error)
                        }
                    }
                } else if (voteData.isKick) {
                    const contract = await api.vote.getExecuteContract(web3Instance, daoContractAddress["VoteExecutionManager"])
                    const wdata = await contract.getImpeachGp(voteData.vote_id)
                    settoaddress(wdata[0])
                    const lst = []
                    for (const i in wdata[3]) {
                        lst.push({
                            address:  wdata[3][i],
                            bonus: wdata[4][i].toNumber() / 100
                        })
                    }
                    setNewVoteData({
                        minRequire: wdata[1].toNumber(),
                        minSuportRequire: wdata[2].toNumber(),
                    })
                    setGplist(lst)

                } else if (voteData.isNewGp) {
                    const contract = await api.vote.getExecuteContract(web3Instance, daoContractAddress["VoteExecutionManager"])
                    const wdata = await contract.getAcceptGp(voteData.vote_id)
                    settoaddress(wdata[0])

                    const lst = []
                    for (const i in wdata[4]) {
                        lst.push({
                            address:  wdata[4][i],
                            bonus: wdata[5][i].toNumber() / 100
                        })
                    }
                    setNewVoteData({
                        minRequire: wdata[2].toNumber(),
                        minSuportRequire: wdata[3].toNumber(),
                    })
                    setGplist(lst)
                } else if (voteData.isTransferManageFee) {
                    const contract = await api.vault.getManageFeeContract(web3Instance, daoContractAddress.ManageFeeVaultManager)
                    const wdata = await contract.withdrawApply(voteData.wid)
                    settoaddress(wdata.proposer)
                    const erc20 = await api.erc20.getContract(web3Instance, wdata.token)
                    seterc20_symbol(await erc20.symbol())
                    settovalule(ethers.utils.formatUnits(wdata.amount.toString(), await erc20.decimals()))

                }
                
            } catch (error) {
                console.error("getMoreVoteInfo failed", error)
            }
        }
        getVoteMore()
    }, [voteData, vaultcontract, daoContractAddress])

    return (
        <div>
            <div className="container">
                <Left history={props.history} handle={props.match.params.handle} owner={props.match.params.owner} removeGroup={true} />
                <BackBrdr>
                    <Button  onClick={handleClicktoVote}><img src={Back} alt="" /><span>{t('vote.Back')}</span> </Button>
                </BackBrdr>
                <SectionLayout>
                    <ul>
                        <li>
                            {
                                voteData.isOpen && (
                                    <div className="succeed"><img src={going} alt=""/>{t('vote.Ongoing')}</div>
                                )
                            }
                            {
                                voteData.status === 2 && t('vote.NotTriggered')
                            }
                            {
                                voteData.status === 1 && (
                                    <div className="succeed">
                                        <img src={succeed} alt=""/>{t('vote.Passed')}
                                    </div>
                                )
                            }
                            {
                                voteData.status === 0 && (
                                    <div className="fail">
                                        <img src={fail} alt=""/>{t('vote.Failed')}
                                    </div>
                                )
                            }

                        </li>
                        <Li>
                            <Titles>{t('vote.Title')}</Titles>
                            <TitleValue>{voteData.title}</TitleValue>
                        </Li>
                        <Li>
                            <Titles>{t('vote.Stats')}</Titles>
                            <CardBrdr>
                                <CardWhite>
                                    <span>{support}</span>
                                    <div>{t('vote.mini')}</div>
                                </CardWhite>
                                <CardWhite>
                                    <span>{supportPercent}</span>
                                    <div>{t('vote.support')}</div>
                                </CardWhite>
                            </CardBrdr>
                        </Li>
                        <Li>
                            <AddBrdr>
                                {
                                    (voteData.isTransfer || voteData.isTransferManageFee) && (
                                        <VoteMore>
                                            <TokenBrdr>
                                                <Titles>{voteData.isTransfer ? t('vote.Transfer') : t('vote.ManageFeeTransfer')} </Titles>
                                                <span>{toValue} {erc20_symbol}</span>
                                            </TokenBrdr>
                                            <div>
                                                <Titles>{t('vote.Recipient')}</Titles>
                                                <Address>
                                                    {toaddress}
                                                </Address>
                                            </div>
                                        </VoteMore>
                                    )
                                }
                                {
                                    voteData.isApprove && (
                                        <VoteMore>
                                            {
                                                approveInfo && <TokenBrdr>
                                                    <Titles>{t('vote.ApproveTitle')}</Titles>
                                                    <span>{approveInfo.amount} {approveInfo.symbol}</span>
                                                </TokenBrdr>
                                            }
                                            <div>
                                                <Titles>{t('vote.Approve')}</Titles>
                                                <Address>
                                                    {toaddress}
                                                </Address>
                                            </div>
                                        </VoteMore>
                                    )
                                }
                                {
                                    (voteData.isKick || voteData.isNewGp) && (
                                        <VoteMore>
                                            <div>
                                                <Titles>{voteData.isKick ? t('vote.Kick') : t('vote.AddGP')}</Titles>
                                                <Address>
                                                    {toaddress}
                                                </Address>
                                                <GpListInfo gplist={gplist} />
                                                <VoteConfigInfo {...newVoteData} />
                                            </div>
                                        </VoteMore>
                                    )
                                }
                                {
                                    voteData.isPeriod && (
                                        <VoteMore>
                                            <div>
                                                <Titles>{t('vote.lockup')}</Titles>

                                                <Address>
                                                    --
                                                </Address>
                                            </div>
                                        </VoteMore>
                                    )
                                }
                            </AddBrdr>
                        </Li>
                        <Li>
                            <Titles>{t('vote.EndTime')}</Titles>
                            <TimeValue>{endtime}</TimeValue>
                        </Li>
                        <Li>
                            <Titles>{t('vote.Description')}</Titles>
                            <Desc>{voteData.desc}</Desc>
                        </Li>
                    </ul>
                    <FlexRht>
                        {
                            !!optionlist.length && <VoteEcharts optionlist={optionlist} />
                        }
                    </FlexRht>
                </SectionLayout>
            </div>

            {/*<ul>*/}

            {/*    <li>*/}
            {/*        <h6>Ends at</h6>*/}
            {/*        <div className='endtime'>{endtime}</div>*/}
            {/*    </li>*/}
            {/*    <li>*/}
            {/*        <h6>{t('VotingNumber')}</h6>*/}
            {/*        <div>{voteid}</div>*/}
            {/*    </li>*/}

            {/*    {*/}
            {/*        need &&*/}
            {/*        <div>*/}
            {/*            <li>*/}
            {/*                <h6>Transaction Status</h6>*/}
            {/*                <div className='address'>{executed?'Successful':'Not triggered'}</div>*/}
            {/*            </li>*/}

            {/*        </div>*/}
            {/*    }*/}
            {/*</ul>*/}
        </div>
    )
}
