import { useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Upload } from 'antd'
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import { uploadImg,uploadProject } from '../../api/apiHTTP'
import ImageUploading from 'react-images-uploading';
import styled from "styled-components";
import addproject from "../../img/addproject.png"

const LOGO = styled.div`
    width: 12rem;
    height: 12rem;
    background: #F4F4FA;
    border-radius: 1.2rem;
    position: relative;
    text-align: center;
    .occupy-img {
        width: 3rem;
        height: 3rem;
        margin-top: 3rem;
    }
    .occupy-text {
        font-size: 1.8rem;
        color: #A6A6B7;
        margin-top: 1rem;
    }
    .mask {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        cursor: pointer;
        
    }
    .image-item {
        width: 100%;
        height: 100%;
    }
`


const getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

const beforeUpload = file => {
    // some limit to img
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        // TODO
      console.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        // TODO
        console.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}
const ImageUploader = forwardRef((props, ref)=>{
    const [loading, setLoading] = useState(false)
    const [imageUrl, setImageUrl] = useState(null)

    const handleChange = info => {
        if (info.file.status === 'uploading') {
            setLoading(true)
            return;
          }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, imageUrl =>{
                setImageUrl(imageUrl)
                setLoading(false)
            });
        }
    }

    const [images, setImages] = useState(null);
    const onChange= (imageList, addUpdateIndex)=>{ setImages(imageList) }

    const goUpload = async ()=>{
        if (!images || images.length === 0) return
        try {
            const result = await uploadImg(images[0].file)
            return result.data.data.fileUpload
        } catch (error) {
            console.error("Upload logo failed: ", error)
        }
    }

    useImperativeHandle(ref, () => ({
        upload: goUpload,
        clear: ()=>{
            setImages([])
        }
    }));


    return (
        // https://www.npmjs.com/package/react-images-uploading
        <ImageUploading
            // acceptType={["png"]}
            value={images}
            onChange={onChange}
            maxNumber={1}
            dataURLKey="dataBs64"
        >
            {({
          imageList,
          onImageUpload,
          onImageRemoveAll,
          onImageUpdate,
          onImageRemove,
          isDragging,
          dragProps,
        }) => (
            <>
                <div className="upload__image-wrapper">
                    <LOGO>
                        <div className='mask' onClick={onImageUpload}>
                            { imageList.length === 0 && <>
                                <img className='occupy-img' src={addproject} alt="" />
                                <div className='occupy-text font-light'>Upload</div>
                            </> }
                            
                        </div>
                        {
                            imageList.map((image, index) => (
                                <img key={index} className="image-item" src={image['dataBs64']} alt="" width="100" />
                            ))
                        }
                        
                    </LOGO>
                    {/* <button onClick={onImageRemoveAll}>Remove all images</button> */}
                </div>
            </>
        )}
            
        </ImageUploading>
    )
})

export default ImageUploader