import React, { forwardRef, useCallback, useState, useImperativeHandle } from "react";
import { useTranslation } from "react-i18next";
import { Button } from "antd"
import { BaseInfoSection, TemplateSection, ModalBox, ModalScrollBody }from "./sections"
import VentureConfigSection from "./venture"
import NormalSection from "./normal"

const PreviewModal = React.memo(forwardRef((props, ref) => {
    const { t } = useTranslation();
    const { doSubmit } = props;
    const [data, setData] = useState({})
    const [show, setShow] = useState(false)

    const clickSubmit = useCallback(
        () => {
            setShow(false)
            doSubmit()
        },
        []
    )

    const closeModal = () => {
        setShow(false)
    }

    useImperativeHandle(ref, () => ({
        show(value) {
            setData(value)
            setShow(true)
        }
    }))


    return (
        <ModalBox width='87rem' visible={show} onCancel={closeModal} footer={null} destroyOnClose={true} maskClosable={false}>
            <div className="preview-title">{t('create.Preview')}</div>
            <ModalScrollBody>
                <BaseInfoSection data={data.baseInfo} />
                <TemplateSection data={data.templateID} />
                {
                    data.templateID === 1 && <VentureConfigSection data={data.venture} />
                }
                {
                    data.templateID === 0 && <NormalSection data={data.data} />
                }
                
                
                <div className="options">
                    <Button className='previous' onClick={closeModal}>{t('create.ReEdit')}</Button>
                    <Button className="nextBtn" type="primary" onClick={clickSubmit}>{t('create.submit')}</Button>
                </div>
            </ModalScrollBody>
        </ModalBox>
    )
}))

export default PreviewModal