export const BaseInfoSection = (props) => {
    const { t } = useTranslation();

    const gateway = useGateway()

    const data = props.data
    const socialData = useMemo(
        () => {
            if (!data) {
                return {}
            }
            const urls = (data.socialUrls || '').split(';')
            return {
                twitter: urls[0],
                discord: urls[1]
            }
        },
        [data]
    )

    return (
        <Section>   
            <div className="head">
                <span></span>
                {t('create.Basicinformation')}
            </div>
            <BaseInfoSectionContent>
                <div className="logo-area">
                    {
                        data.logo ? <img src={gateway+data.logo} alt="" /> : <ReactSVG className='logo' src={LogoSVG} />
                    }
                </div>
                <div className="right">
                    <div className="dao-name">{data.name}</div>
                    <div className="social">
                        {
                            socialData.twitter && <>
                                <a href={`https://twitter.com/${socialData.twitter}`} target="_blank" rel="noreferrer" >
                                        <img src={twitterBlue} alt="" />
                                </a>
                            </>
                        }
                        {
                            socialData.discord && <>
                                <a href={socialData.discord} target="_blank" rel="noreferrer"><img src={discordBlue} alt="" /></a>
                            </>
                        }
                    </div>
                    <div className="dao-desc">{data.desc}</div>
                </div>
            </BaseInfoSectionContent>
        </Section>
    )
}



const TemplateSectionContent = styled.div`
    padding: 2.3rem 0 3.3rem 4rem;
    background: rgba(225, 226, 232, 0.15);
    border-radius: 4px 12px 12px 12px;
    color: #212758;
    .title {
        margin-left: 0.7rem;
        margin-bottom: 2rem;
        font-size: 2.4rem;
        line-height: 2.8rem;
    }
    img {
        width: 3.2rem;
        margin-right: 0.6rem;
        position: relative;
        top: -0.3rem;
    }
    span {
        margin-right: 1rem;
        font-size: 1.8rem;
    }
`
export const TemplateSection = (props) => {
    const { t } = useTranslation();
    const data = props.data
    const templateInfo = useMemo(
        () => {
            return templateList.filter(d => d.id === data)[0]
        },
        [data]
    )

    return (
        <Section>   
            <div className="head">
                <span></span>
                {t('create.TemplateSelection')}
            </div>
            <TemplateSectionContent>
                {
                    templateInfo && <>
                        <div className="title">{ t(templateInfo.name)}</div>
                        <div>
                            {templateInfo.components.map(item => (
                                <span key={item.name}><img src={item.img} alt=""/> {t(item.name)}</span>
                            ))}
                        </div>
                    </>
                }
            </TemplateSectionContent>
        </Section>
    )
}

