const ManagerShowList = [
    "AuthManager", "VaultManager", "VoteManager", "OrgManager", "VenturesManager", "VenturesStockManager"
]

export default function About(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { orgcontract, maincontract, whoAmI, isVentureDao, daoContractAddress, web3Instance, Account, venturecontract } = state;

    const [id, setAId] = useState(null);
    const [moderators, setModerators] = useState([]);
    const [moderatorShow, setmoderatorShow] = useState(true);
    const [activelist, setActivelist] = useState([]);
    const [balancelist, setbalancelist] = useState([]);
    const [balanceshow, setbalanceshow] = useState(true);
    const [contractshow, setcontractshow] = useState(true);
    const [owner, setowner] = useState('');

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [contractArr, setcontractArr] = useState([]);
    const [tokenlist, settokenlist] = useState([]);

    const [errorShow, seterrorShow] = useState(false);
    // const [nativeToken, setnativeToken] = useState(0);
    const [errorTips, seterrorTips] = useState('');

    const [moreDaos, setMoreDaos] = useState(false);
    const [assets, setAssets] = useState({});
    const [loadingAssets, setLoadingAssets] = useState(true);
    const [errorTokens, setErrorTokens] = useState(null)


    const updateDaoID = useCallback(
        id=>setAId(id),
        []
    )

    useEffect(async () => {
        if (!maincontract) return
        setcontractArr([]);
        setcontractshow(true);
        setbalancelist([]);
        setbalanceshow(true);

    }, [props.match.params.handle, maincontract]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);
   
    const getDaoContract = () => {
        if (!daoContractAddress) return
        let lst = []
        ManagerShowList.forEach(m=>{
            if (daoContractAddress[m]) {
                lst.push({
                    name: m,
                    address: daoContractAddress[m]
                })
            }
        })
        setcontractArr(lst)
        setcontractshow(false);
    }
   
    const updateToken = useCallback(
        (tokenlist) => {
            setbalanceshow(false)
            setbalancelist(tokenlist)
        },
        []
    )

    const getAllAssets = async (tokenlist)=>{
        const fundAddress = await venturecontract.TokenType()
        const fundContract = await api.erc20.getContract(web3Instance, fundAddress)
        const symbol = await fundContract.symbol()
        const deci = await fundContract.decimals()
        let total_value = 0
        const errors = []
        for (const token of tokenlist) {
            if (token.address === fundAddress) {
                total_value += Number(token.balance)
                continue
            }
            try {
                const contract = await api.erc20.getContract(web3Instance, token.address)
                const num = await api.venture.getAmountOutMin(web3Instance, web3Instance.getSigner(Account), {
                    tokenIn: token.address,
                    tokenOut: fundAddress,
                    amountIn: ethers.utils.parseUnits(String(token.balance), await contract.decimals())
                })

                total_value += Number(ethers.utils.formatUnits(num, deci))
            } catch (error) {
                errors.push(token.symbol)
            }
        }
        return {
            errors: errors.slice(0, 5).join(', ') + (errors.length>5?'...':''),
            assets: {total: getShortDisplay(total_value), symbol}, 
        }
    }

    useEffect(async()=>{
        if (!venturecontract || !isVentureDao || balancelist.length === 0) return
        try {
            const {assets, errors} = await getAllAssets(balancelist)
            setAssets(assets)
            setLoadingAssets(false)
            if (errors.length > 0) setErrorTokens(errors)
        } catch (error) {
            console.error('get getAllAssets failed', error)
        }
    }, [balancelist, venturecontract, isVentureDao])

    useEffect(async()=>{
        if (!id || !mainConnect) return
        // await getDaoData();
    }, [id, mainConnect])

    useEffect(() => {
        getDaoContract()
    }, [daoContractAddress]);


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(async () => {
        if (!orgcontract) return
        try {
            const {lst, len} = await api.org.getDaoModeratorList(orgcontract, 0, 1000)
            setModerators(lst);
            setmoderatorShow(false);
        } catch (error) {
            console.error('getDaoModeratorList failed', error)
        }
    }, [orgcontract])


    const switchKey = (key) => {
        let str = '';
        let img = '';
        switch (key) {
            case 'base_addr':
                str = 'Base';
                img = BASE;
                break;
            case 'GovTokenManager':
                str = 'ERC20';
                img = ERC20;
                break;
            case 'OrgManager':
                str = 'ORG';
                img = ORG;
                break;
            case 'VaultManager':
                str = 'Vault';
                img = VAULT;
                break;
            case 'VoteManager':
                str = 'Vote';
                img = VOTE;
                break;
            case 'AuthManager':
                str = 'Auth';
                img = AUTH;
                break;
            case 'VenturesManager':
                str = 'Venture';
                img = VENTURE;
                break;
            case 'VenturesStockManager':
                str = 'TOKEN';
                img = ERC20;
                break;
            default:
                str = key;
                img = '';
                break;
        }
        return {
            addr: str.toUpperCase(),
            img
        };
    }

    const setDaoOwner = useCallback(
        (o) => {
            o && setowner(o)
        },
        []
    )


    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip>{errorTips}</Tip>
            </Modal>

            <div className='container'>
                <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} setDaoOwner={setDaoOwner} updateToken={updateToken} />
                <section>
                    {
                        isVentureDao && loadingAssets && <>
                            <div className="titleTop">{t('about.TotalAssests')}</div>
                            <LoadingNew />
                        </>
                    }
                    {
                        isVentureDao && !loadingAssets && <>
                            <div className="titleTop">{t('about.TotalAssests')}</div>
                            <Assest>
                                <div className="asset-value">{assets.total}</div>
                                <div className="asset-symbol">{assets.symbol}</div>
                                {
                                    errorTokens && <div className='asset-error'>
                                        {errorTokens} {t('about.AssetsValue')}
                                    </div>
                                }
                            </Assest>
                            
                        </>
                    }
                    {
                        !isVentureDao && <>
                            <div className="titleTop">{t('about.Balance')}</div>
                            <Ul>
                            {
                                balanceshow && <LoadingNew />
                            }
                            {
                                !balanceshow && balancelist.map((item, index) =>
                                    <li key={`balance_${index}`}>
                                        <BalanceNum>{item.balanceDisplay}</BalanceNum>
                                        <Symbol>{item.symbol}</Symbol>
                                        {
                                            <Address>
                                                {item.address}
                                                <CopyStr address={item.address} />
                                            </Address>
                                        }

                                    </li>
                                )
                            }
                            </Ul>
                        </>
                    }
                </section>
                <section>
                    <div className="titleTop">{isVentureDao ? t('org.Master') : t('org.Moderators')}</div>
                    <UlMdrt>

                        {
                            moderatorShow && <LoadingNew />
                        }
                        {
                            !moderatorShow && owner && (
                                <li>
                                    <img src={OwnerImg} alt="" />
                                    <div>
                                        <Names>{t('about.Owner')}</Names>
                                        <Address>{owner}</Address>
                                    </div>
                                </li>
                            )
                        }
                        {
                            !moderatorShow && moderators.map((item) => <li key={item.address}>
                                <img src={AdminImg} alt="" />
                                <div>
                                    <Names>{item.name}</Names>
                                    <Address>{item.address}</Address>
                                </div>
                            </li>)
                        }
                    </UlMdrt>
                </section>
                <section>
                    <div className="titleTop">{t('about.Contracts')}</div>
                    {
                        contractshow && <LoadingNew />
                    }
                    {
                        !contractshow && <UlContr>{
                            contractArr.map((item) => (
                                <li key={`contract_${item.address}`}>
                                    <Imglft src={switchKey(item.name).img} alt="" />
                                    <ContractName>{switchKey(item.name).addr} </ContractName>
                                        <RhtBrdr>
                                            <Address>{item.address}</Address>
                                            <CopyStr address={item.address} alt="" />
                                        </RhtBrdr>

                                </li>
                            ))
                        }
                        </UlContr>
                    }
                </section>
                {
                    (!isModerator && !isOwner && !isMember) && <div>
                        <Other>{t('about.others')}</Other>
                        <MoreDaos history={props.history} exceptId={id} title={t('about.explorer')} sizeStatus="size"/>
                    </div>
                }

            </div>
        </div>
    )
}
