import React, {useState, useRef, useCallback} from "react";
import {Button, InputNumber, Modal,Radio, DatePicker} from "antd";
import styled from "styled-components";
import moment from "moment";
import { useWeb3 } from "../../api/contracts";
import Estimate from "./estimate";
import api from "../../api";
import { ethers } from "ethers"
import {useTranslation} from "react-i18next";
import Whitelist from "./whiteList"

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .myDatePicker {
        height: 7.6rem;
        background: #F4F4FA;
        border-radius: 1.2rem;
        border: none;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`


const LastTr = styled('div')`
 
  margin-top: 4.4rem;
  color: #010643;
  .ant-radio-group{
    width: 100%;
     display: flex;
    justify-content: space-between;
  }
`


export default function RaiseLps(props) {
    const { show ,closeRaise, fund } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { Account, web3Instance, daoOrg } = state;

    const [value, setValue] = useState(0);

    const [maxDep, setMaxDep] = useState(null)
    const [minDep, setMinDep] = useState(null)

    const [raiseTime, setRaiseTime] = useState(null)
    const [eHeight, setEHeight] = useState(null)

    const [whitelistVisible, setWhitelistVisible] = useState(null)
    const [treeData, setTreeData] = useState({
        root: '',
        recordsCord: ''
    })

    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }

    const handleRaiseTime = (v)=>{
        const dateTime = Date.parse(v._d)
        // timestamp(s)
        setRaiseTime(Math.round(dateTime/1000))
    }

    const onChange = e => {
        console.log('radio checked', e.target.value);
        setValue(e.target.value);
        if (e.target.value === 1) setWhitelistVisible(true)
    };

    const handleClose = () =>{
        closeRaise()
    }
    const confirmRaise = async ()=>{
        handleClose()
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.ConfigLP')}` });

            const erc20 = await api.erc20.getContract(web3Instance, fund.address);
            const deci =  await erc20.decimals()

            const signer = web3Instance.getSigner(Account)
            await api.venture.configLPraise(daoOrg, signer, {
                height: raiseTime, 
                minDep: ethers.utils.parseUnits(String(minDep), deci), 
                maxDep: ethers.utils.parseUnits(String(maxDep), deci), 
                root: treeData.root || ethers.constants.HashZero,
                recordsCode: treeData.recordsCode || '',
            })
            props.getVenturePeriod()
        } catch (error) {
            console.error(error)
            const msg = (error.data && error.data.message) || 'failed'
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.ConfigFailed')}: ${msg}`, closable: true } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    const formatDate = useCallback((m)=>{
        return m.format('YYYY-MM-DD HH:mm:ss') + ' GMT' + m.format('ZZ')
    }, [])

    return (
        <>
            <Whitelist show={whitelistVisible} closeModal={()=>{setWhitelistVisible(false)}} updateWhiteList={v=>setTreeData(v)}/>
            <ModalBox visible={show} onCancel={handleClose} footer={null}  destroyOnClose={true} maskClosable={false}>
                <div className="title">
                    <span >{t('venture.RaiseLPs')}</span>
                </div>

                <div className="label">{t('venture.RaiseTime')}</div>
                <div className="inputBrdr">
                    <DatePicker
                        className="myDatePicker"
                        placeholder={t('venture.RaiseTime')}
                        style={{ width: '100%' }}
                        format={formatDate}
                        allowClear={false}
                        disabledDate={disabledDate}
                        onChange={handleRaiseTime}
                        showTime={true}
                        showNow={false} />
                </div>
                {/* <Estimate time={raiseTime} updateHeight={v=>setEHeight(v)} /> */}

                <div className="label">{t('venture.MaxLP')}</div>
                <div className="inputBrdr">
                    <InputNumber 
                        placeholder={t('venture.enter')}
                        controls={false}
                        onChange={(v)=>setMaxDep(v)}
                    />
                    <span className='suffix'>{fund.symbol}</span>
                </div>
                <div className="label">{t('venture.MinLP')}</div>
                <div className="inputBrdr">
                    <InputNumber 
                        placeholder={t('venture.enter')}
                        controls={false}
                        onChange={(v)=>setMinDep(v)}
                    />
                    <span className='suffix'>{fund.symbol}</span>
                </div>
                <LastTr>
                    <Radio.Group onChange={(e)=>onChange(e)} value={value}>
                        <div><Radio value={0}>Do not use white list</Radio></div>
                        <div><Radio value={1}>Add LP white list</Radio></div>
                    </Radio.Group>
                </LastTr>

                <NextBrdr >
                    <Button type="primary" disabled={!maxDep || !minDep || !raiseTime} onClick={confirmRaise} style={{ width: '100%', marginTop: '3rem' }}>
                        {t('venture.Submit')}
                    </Button>
                </NextBrdr>
            </ModalBox>
        </>
    )
}
