export const templateList = [
    {
        id: 1,
        name: "create.VentureTemplate",
        components: [...components, {
            name: "VENTURE",
            vault: "",
            img: VENTURE,
        }]
    },
    {
        id: 0,
        name: "create.DaoTemplate",
        components: components
    },

]

export default function SecondStep(props) {
    const { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { web3InstanceState } = state;

    const [selected, setselected] = useState(1);

    const toThirdStep = () => {
        props.handleTemplate(selected)
        props.handlerSet(3);
        sessionStorage.setItem('secondStep', selected)
    }
    const toFirstStep = () => {
        props.handlerSet(1)
    }
    const handleSelect = (e) => {
        let id = Number(e.currentTarget.id);
        let template = templateList.filter(item => item.id === id);
        setselected(template[0].id)
    }

    useEffect(() => {
        if (web3InstanceState !== 'READY') return;
        dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, [web3InstanceState]);


    useEffect(() => {
        const secondStep = JSON.parse(sessionStorage.getItem('secondStep'))
        setselected(secondStep || 1)
    }, []);

    return <div className="content">
        <Content>
            <StepContentArea>
                <Info>
                    <div className="title">{t('create.TemplateSelection')}</div>
                    {/*<div className='detail'>{t('chooseTemplate')}</div>*/}
                </Info>
                <div className="listBrdr">
                    {
                        templateList.map(i => (
                            <dl key={i.id} id={i.id} className={selected === i.id ? 'active' : ''}>
                                <dt >{t(i.name)}</dt>
                                <div className="ddBox">
                                    {i.components.map(item => (
                                        <dd key={item.name}><img src={item.img} alt="" /> {item.name}</dd>
                                    ))}
                                </div>
                                {selected !== i.id && <Button className="useBtn" id={i.id} onClick={handleSelect}>
                                    {t('create.use')}
                                </Button>}
                            </dl>
                        ))
                    }
                </div>
            </StepContentArea>
        </Content>
        <StepFooter>
            <Button className="previous" style={{ marginRight: '3rem' }} onClick={toFirstStep}>
                <img className="left" src={left} alt="" />
                {t('create.think')}
            </Button>
            <Button type="primary" className='nextBtn' onClick={toThirdStep}>
                {t('create.Next')}
                <img src={right} alt="" />
            </Button>
        </StepFooter>
    </div>
}
