import React, { useState, forwardRef, useImperativeHandle, useRef } from 'react'
import { Input } from 'antd'
import { useTranslation } from 'react-i18next';
import { GpInfo } from './styles'
import publicJs from '../../../utils/publicJs';
import VentureVoteConfig from '../../create/ventureVoteConfig'
import { ethers } from 'ethers';

const AddGP = React.memo(forwardRef((props, ref)=>{
    const { gplist, updateGplist } = props
    const { t } = useTranslation();

    const voteRef = useRef();

    const [newGp, setNewGp] = useState()

    const handleSetBonus = (e, index)=>{
        let newArray = [...gplist];
        const { name, value } = e.target;
        newArray[index].bonus = Number(value);
        updateGplist(newArray)
    }


    useImperativeHandle(ref, () => ({
        checkValid() {
            return newGp && newGp.address && newGp.bonus && !voteRef.current.checkInvalid() && ethers.utils.isAddress(newGp.address)
        },
        getData() {
            const gp_address = gplist.map(l=>l.address)
            const gp_bonus = gplist.map(l=>l.bonus*100)
            gp_address.push(newGp.address)
            gp_bonus.push(newGp.bonus*100)
            return {
                newGP: newGp.address,
                gpList: gp_address, 
                gpBonus: gp_bonus, 
                newVoteConfig: voteRef.current.newVoteConfig
            }
        }
    }));

    return (
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.GPAddress')}</div>
            </div>
            <div className='inputBrdr rht'>
                <Input
                    autoComplete="off"
                    onChange={e=>{setNewGp({...newGp, address:e.target.value})}}
                />
            </div>
            {
                newGp && newGp.address && <>
                    <GpInfo>
                        <div className="line top">
                            <div className='label'>{t('vote.GpInfo')}</div>
                        </div>
                        <ul>
                            <li>
                                <div className="line">
                                    <div className="label lft"><span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(newGp.address)}</div>
                                    <div className='inputBrdr rht'>
                                        <div className="label">{t('vote.bonus')}</div>
                                        <Input
                                            type="number"
                                            placeholder={t('vote.bonus')}
                                            onChange={(e) => setNewGp({...newGp, bonus:Number(e.target.value)})}
                                            autoComplete="off"/>
                                        <span className='suffix'>%</span>
                                    </div>
                                </div>
                            </li>
                            {
                                gplist.map((item, i)=>{
                                    return <li key={i}>
                                                <div className="line">
                                                    <div className="label lft"> <span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(item.address)}
            
                                                    </div>
            
                                                    <div className='inputBrdr rht'>
                                                        <div className="label">{t('vote.bonus')}</div>
                                                        <Input
                                                            type="number"
                                                            placeholder={t('vote.bonus')}
                                                            onChange={(event) => handleSetBonus(event, i)}
                                                            autoComplete="off"/>
                                                        <span className='suffix'>%</span>
                                                    </div>
                                                </div>
                                            </li>
                                })

                            }
                        </ul>
                    </GpInfo>
                    <VentureVoteConfig ref={voteRef} gp={gplist.length+1} titleClass='label' />
                </>
            }
            
        </div>
    )
}))

export default AddGP