import React, { useCallback, useEffect, useMemo, useState, forwardRef, useImperativeHandle } from "react"
import { useTranslation } from "react-i18next";
import { Tabs, Pagination, Select } from "antd"
import { useWeb3 } from "../../api/contracts";
import { getAllDaos } from "../../api/apiHTTP"
import api from "../../api";
import { ethers } from "ethers"
import useGateway from "../../hooks/useGateway"
import useNetwork from "../../hooks/useNetwork"
import LogoSVG from '../../img/logoSubdao.svg'
import { ReactSVG } from 'react-svg'
import { DAOS, PageBrdr, DaosNoData, WithTabs, SortSelect } from './Styles'
import getBestDaoIds from './GetBestDaos'
import LoadingNew from "../loadingNEW";

const { TabPane } = Tabs;

const DaoTabList = React.memo(forwardRef((props, ref) => {
    const { t } = useTranslation();

    const sortList = [
        { name: t('mydao.SortRaised'), value: 'raisedAmount' },
        { name: t('mydao.SortTime'), value: 'createTime' }
    ]

    const { state, dispatch } = useWeb3();
    const { maincontract, Account, certifiedDAOs } = state;

    const [allList, setAllList] = useState([])
    const [ccList, setccList] = useState([])
    const [loading, setLoading] = useState(true)

    const [pageTotal, setPageTotal] = useState(0);
    const [page, setPage] = useState(1);

    const [selectTabKey, setSelectTabKey] = useState('2')
    const [selectSort, setSelectSort] = useState(sortList[1].value)
    const [keyword, setKeyword] = useState()

    const pageSize = 10

    const gateway = useGateway()
    const networkData = useNetwork()

    const getDaosByIds = useCallback(
        async (daoFactoryContract, ids, gateway) => {
            if (ids.length === 0) {
                return []
            }
            try {
                const datas = await api.dao.getDaosById(daoFactoryContract, ids)
                const lst = datas.daos.map((d, i) => {
                    return {
                        name: d.name,
                        logo: (gateway && d.logo) ? <img src={gateway + d.logo} alt="" /> : (<ReactSVG className='svg' src={LogoSVG} />),
                        desc: d.desc,
                        key: d.id.toString(),
                        id: d.id,
                        handle: datas.aliases[i],
                        owner: d.owner,  // account
                    }
                })
                return lst
            } catch (error) {
                console.error('getDaosById failed', error)
                return []
            }

        },
        []
    )
    const getHandlesByIds = useCallback(
        async (daoFactoryContract, ids) => {
            const reqs = ids.map(id=>daoFactoryContract.daoIdToAlias(ethers.BigNumber.from(id)))
            try {
                return await Promise.all(reqs)
            } catch (error) {
                console.error('daoIdToAlias failed', error)
                return []
            }
        },
        []
    )

    const getCertifiedDaos = async () => {
        let all_list = []
        if (certifiedDAOs && certifiedDAOs.length > 0) {
            all_list = certifiedDAOs
        } else {
            setLoading(true)
            const ids = await getBestDaoIds(networkData.daos.url, networkData.daos.key, 'certified')
            all_list = await getDaosByIds(maincontract, ids, gateway)
            const alias = await getHandlesByIds(maincontract, ids)
            if (alias.length > 0) {
                all_list.forEach((d,i)=>{d.handle=alias[i]})
            }
            dispatch({ type: 'CERTIFIEDDAOS', payload: all_list })
        }

        if (keyword) {
            all_list = all_list.filter(dao=>dao.name === keyword)
            setccList(all_list)
        } else {
            const start = (page - 1) * pageSize
            setccList(all_list.slice(start, start + pageSize))
        }
        setPageTotal(all_list.length)
        setLoading(false)
    }

    const getAlldaos = async () => {
        try {
            setLoading(true)
            let res
            if (keyword) {
                res = await getAllDaos(Account, 0, 0, selectSort, keyword)
                setPageTotal(res.length)
            } else {
                res = await getAllDaos(Account, page - 1, pageSize, selectSort, keyword)
                setPageTotal(Number(res.totalCount && res.totalCount.count))
            }
            const ids = res.daoOrgInstances.map(o => ethers.BigNumber.from(o.id))
            const list = await getDaosByIds(maincontract, ids, gateway)
            list.forEach((d,i)=>{d.handle=res.daoOrgInstances[i].daoalias})
            setAllList(list)
        } catch (error) {
            console.error('getAllDaos failed', error)
            setAllList([])
        } finally {
            setLoading(false);
        }
    }

    const requestDaoList = useCallback(
        (key) => {
            if (key === '1') {
                getCertifiedDaos()
            } else if (key === '2') {
                getAlldaos()
            }
        },
        [getCertifiedDaos, getAlldaos]
    )


    useEffect(() => {
        if (!networkData || !gateway) return
        requestDaoList(selectTabKey)
    }, [page, selectTabKey, networkData, selectSort, keyword, gateway])

    const onChangeSelect = useCallback(
        key => {
            setSelectTabKey(key)
            setPage(1)
            setPageTotal(0)
        },
        []
    )

    const handleSelectSort = useCallback(
        v => setSelectSort(v),
        []
    )

    const SortSelectComponent = (
        <SortSelect onChange={handleSelectSort}  defaultValue={sortList[1].value}>
            {
                sortList.map(item => {
                    return <Select.Option key={item.value} value={item.value}><div style={{fontFamily: 'HelveticaNeue', lineHeight: '5rem'}}>{item.name}</div></Select.Option>
                })
            }
        </SortSelect>
    )

    const tabBarExtraContent = useMemo(
        ()=>{
            return selectTabKey === '1' ? null : SortSelectComponent
        },
        [selectTabKey]
    )

    useImperativeHandle(ref, () => ({
        // search
        goSearch(kw) {
            setKeyword(kw)
            setPage(1)
        }
    }))


    return (
        <WithTabs defaultActiveKey={selectTabKey} onChange={onChangeSelect} tabBarExtraContent={tabBarExtraContent}>
            <TabPane tab={t('mydao.Verified')} key="1">
                <DAOS>
                    {
                        loading && <LoadingNew />
                    }
                    {
                        !loading && ccList.map((item, i) =>
                            <div key={item.id} className="daoItem" onClick={() => { props.handleClicktoAbout(item.handle || item.id) }}>
                                {item.logo}
                                <div className="title">{item.name}</div>
                                <div className="detail">{item.desc}</div>
                            </div>
                        )
                    }
                    {
                        !loading && ccList.length === 0 && <DaosNoData text={t('mydao.noDaos')} />
                    }
                    {
                        ccList.length > 0 && (
                            <PageBrdr>
                                <Pagination defaultCurrent={page} total={pageTotal} pageSize={pageSize} onChange={p => setPage(p)} />
                            </PageBrdr>
                        )
                    }
                </DAOS>
            </TabPane>
            <TabPane tab={t('mydao.All')} key="2">
                <DAOS>
                    {
                        loading && <LoadingNew />
                    }
                    {
                        !loading && allList.map((item, i) =>
                            <div key={item.id} className="daoItem" onClick={() => { props.handleClicktoAbout(item.handle || item.id) }}>
                                {item.logo}
                                <div className="title">{item.name}</div>
                                <div className="detail">{item.desc}</div>
                            </div>
                        )
                    }
                    {
                        !loading && allList.length === 0 && <DaosNoData text={t('mydao.noDaos')} />
                    }
                    {
                        allList.length > 0 && (
                            <PageBrdr>
                                <Pagination defaultCurrent={page} total={pageTotal} pageSize={pageSize} onChange={p => setPage(p)} />
                            </PageBrdr>
                        )
                    }
                </DAOS>
            </TabPane>
        </WithTabs>
    )
}))
export default DaoTabList