import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Modal, Input, Select } from 'antd';
import { ethers } from "ethers"
import api from "../../api";
import { useWeb3 } from "../../api/contracts";

import { useTranslation } from "react-i18next";

const Withdraw = forwardRef((props, ref) => {
    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, vaultcontract,erc20contract, daoOrg } = state;

    const [selected, setSelected] = useState(null);
    const [address, setaddress] = useState('');
    const [amount, setamount] = useState('');

    const [list, setList] = useState([]);

    let { t } = useTranslation();

    useEffect(()=>{
        setList(props.tokenlist)
        if (props.tokenlist.length > 0) {
            setSelected(props.tokenlist[0].address)
        }
    }, [props.tokenlist])

    const handleChange = (e) => {
        const { name, value } = e.target;
        let str = `set${name}`
        eval(str)(value)
    }
    const handleConfirm = async () => {
        dispatch({ type: 'LOADINGTYPE', payload: 'Transferring' });
        props.handleClose();

        const signer = web3Instance.getSigner(Account)

        try {
            const erc20_rw = await api.erc20.getContract(signer, selected);
            await api.vault.transfer2(erc20_rw, address, amount, await erc20_rw.decimals())
            
            dispatch({ type: 'BALANCE', payload: true });
            setamount('')
            setaddress('')
            props.setShow()
        } catch (error) {
            dispatch({ type: 'MSGTYPE', payload: { msg: `Transfer: ${error.message}` } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }
    const handleSelect = (value) => {
        setSelected(value)
    }
    useImperativeHandle(ref, () => ({
        resultToVault: (newVal) => {
            return {
                address,
                amount
            }
        },
        amountToNull: () => {
            setamount('')
            setaddress('')
        }
    }));

    let { handleClose, showTips } = props;
    return (
        <div>
            <Modal visible={showTips} onCancel={handleClose} footer={null}>
                <div className="title">
                    {/* <img src={sender} alt="" /> */}
                    <span>Transfer</span>
                </div>

                <div className="label">Token</div>
                <div className="inputBrdr">
                    <Select style={{ width: '100%' }} defaultValue={selected} onChange={handleSelect} placeholder='Token'>
                        {
                            list.map((i) =>
                                <Select.Option value={i.address} key={i.address}>
                                    <div style={{ display:'flex', alignItems:'center'}}>
                                        <div style={{ display:'flex', flexDirection:'column', flexGrow:1}}>
                                            <div style={{lineHeight: '3.55rem',fontFamily: 'HelveticaNeue',fontWeight: 400, color: '#10164B'}}>{i.symbol}</div>
                                            <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${i.address.slice(0,5)}…${i.address.slice(-5)}`}</div>
                                        </div>
                                        <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>Available: {i.balance}</div>
                                    </div>
                                </Select.Option>
                            )
                        }
                    </Select>
                </div>

                <div className="label">Address</div>
                <Input
                    placeholder='Address'
                    value={address}
                    name='address'
                    onChange={handleChange}
                />

                <div className="label">Amount</div>
                <Input
                    placeholder="Amount"
                    value={amount}
                    name='amount'
                    onChange={handleChange}
                />

                <Button disabled={!amount || !address || !selected} type="primary" onClick={() => handleConfirm()} style={{ width: '100%', marginTop: '3.9rem' }}>Submit</Button>
            </Modal>
        </div>
    )

})
export default Withdraw;
