import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Modal, Input } from 'antd';

import api from "../api";
import { useWeb3 } from "../api/contracts";

import { useTranslation } from "react-i18next";

const Transfer = forwardRef((props, ref) => {
    const { state, dispatch } = useWeb3();
    const { maincontract, daoOrg, orgcontract, web3Instance, Account } = state;

    const [address, setAddress] = useState('');
    const [check, setCheck] = useState(false);

    let { t } = useTranslation();

    let { handleClose, showTips, daoId } = props;

    const handleChange = (e) => {
        const { value } = e.target;
        setAddress(value)
    }
    const handleConfirm = async () => {
        dispatch({ type: 'LOADINGTYPE', payload: `${t('transfer.Transferring')}` });
        props.handleClose()
        try {
            const signer = web3Instance.getSigner(Account)
            await api.org.transferOwnership(maincontract, signer, parseInt(daoId), address)
            dispatch({ type: 'LOADINGTYPE', payload: null });
            window.location.reload();
        } catch (error) {
            console.error("transferOwnership failed", error)
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('transfer.ownership')}: ${(error.data && error.data.message) || 'failed'}`, closable: true } });
            dispatch({ type: 'LOADINGTYPE', payload: null });
            setAddress('')
        }

    }
    useEffect(()=>{
        if(!check) return;
        const setdeposittrs = async () => {
            await api.org.transferOwnership(orgcontract, address, (result) => {
                if (result) {
                    dispatch({ type: 'LOADINGTYPE', payload: null });
                    setCheck(false);
                    setAddress('')
                    window.location.reload();

                }
            }).catch((error) => {
                dispatch({ type: 'MSGTYPE', payload: { msg: `${t('transfer.ownership')}: ${error.message}`, closable: true } });
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setAddress('')
            });
        };
        setdeposittrs();
    },[check])

    useImperativeHandle(ref, () => ({
        resultToVault: () => {
            return {
                address
            }
        },
        amountToNull: () => {
            setAddress('')
        }
    }));

    return (
        <div>
            <Modal visible={showTips} onCancel={handleClose} footer={null}>
                <div className="title">
                    {/* <img src={sender} alt="" /> */}
                    <span>{t('transfer.ownership')}</span>
                </div>

                <div className="label">{t('transfer.Address')}</div>
                <div className="inputBrdr">
                    <Input
                        placeholder={t('transfer.Address')}
                        value={address}
                        name='address'
                        onChange={handleChange}
                    />
                </div>

                <Button type="primary" onClick={() => handleConfirm()} style={{ width: '100%', marginTop: '10rem' }}>{t('transfer.Submit')}</Button>
            </Modal>
        </div>
    )
})
export default Transfer
