import React, { useEffect, useState, useCallback } from 'react';
import { useWeb3 } from "./api/contracts";
import api from "./api";

import Left from './components/left';

import Addnew from './components/org/addNew';
import AddAuth from './components/org/addAuth';
import AddApply from './components/org/addApply';
import ApplyList from './components/org/ApplyList';
import ApplyTips from './components/org/ApplyTips';
import AddBatch from './components/org/addbatch';
import LoadingNew from "./components/loadingNEW";


import { Badge, Button } from 'antd';
import OwnerImg from "./img/owner.png";
import AdminImg from "./img/admin.png";
import AddP from "./img/addPeople.png";
import Remove from './img/remove.png';
import styled from "styled-components";

import TriggerBtn from "./img/switchClose.png";
import TriggerBtnActive from "./img/switchOpen.png";
import AuthImg from "./img/authHover.png";
import {useTranslation} from "react-i18next";

const UlMdrt = styled.ul`
    color: #10164B;
    display: flex;
    margin-left: -4rem;
    flex-wrap: wrap;
    li{
        width: 33rem;
        height: 14rem;
        background: #FFFFFF;
        box-shadow: 0 0 1rem 0 rgba(16, 22, 75, 0.05);
        border-radius: 2.4rem;
        padding: 1rem 2rem 1rem 1rem ;
        box-sizing: border-box;
        word-break: break-all;    
        margin:0 0 3rem 4rem;
        display: flex;
        align-content: center;
        position: relative;
        .imgAuth{
        position: absolute;
        right: 2rem;
        top: 1rem;
        width: 3.2rem;
        cursor: pointer;
        display: none;
        }
        .imgRemove{
        position: absolute;
        right: -1.6rem;
        top: -1.6rem;
        width: 3.6rem;
        cursor: pointer;
        }
        .names{
            font-size: 2rem;
            font-weight: 300;
            line-height: 2.4rem;
            margin: 1rem 0;
            font-family: HelveticaNeue;
        }
        &:hover{
        background: #FFEFF7;
        .names{
            color: #D51172;
        }
        .imgAuth{
        display: block;
        }
        }
    }
`;


const NamesAdd = styled.div`
    font-size: 2rem;
    font-weight: 400;
    font-family: HelveticaNeue;
    margin-top: 4.6rem;
    cursor: pointer;
`;

const Address = styled.div`
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 2.2rem;
    opacity: 0.4;
`;

const FirstLine = styled.div`
  position: relative;
`;

const BtnRht = styled.div`
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    align-content: center;
    padding-right: 3rem;
    button{
        width: 14rem;
        height: 4rem;
        font-weight: 300;
        font-size: 1.8rem;
        margin-left: 2rem;
        padding: 0;
        line-height: 3rem;
        &.active{
            border-radius: 0.4rem;
            border: 0.1rem solid #D52473;
            color: #d52473;
    }
  }
`;

const SwitchBrdr = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-start;
    line-height: 3rem;
    img{
        cursor: pointer;
    }
    div{
        font-size: 1.8rem;
    }
`;

const SwitchBtn = styled.img`
    width: 4.2rem;
    height: 2.2rem;
    margin-right: 1rem;
`

export default function Org(props) {
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, daoOrg, orgcontract, authcontract, erc20contract,maincontract,whoAmI, isVentureDao } = state;

    const [addshow, setaddshow] = useState(false);
    const [authshow, setauthshow] = useState(false);
    const [applyshow, setapplyshow] = useState(false);
    const [addapplyshow, setaddapplyshow] = useState(false);
    const [batchshow, setbatchshow] = useState(false);
    const [typeName, settypeName] = useState('');
    const [tips, setTips] = useState('');
    const [showTips, setshowTips] = useState(false);
    const [manage, setManage] = useState(false);

    const [id, setId] = useState(null);
    const [list, setlist] = useState([]);
    const [listStatus, setlistStatus] = useState(false);
    const [memberlist, setmemberlist] = useState([]);
    const [memberlistStatus, setmemberlistStatus] = useState(false);
    const [authlist, setauthlist] = useState([]);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);
    const [applyAuth, setapplyAuth] = useState(false);
    const [triggerStatus, settriggerStatus] = useState(false);
    const [address, setaddress] = useState('');
    const [owner, setowner] = useState('');
    const [errorShow, seterrorShow] = useState(false);
    const [num, setnum] = useState(null);


    useEffect(() => {
        if (maincontract == null || orgcontract == null || whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);

    }, [id, maincontract,orgcontract,whoAmI]);
    useEffect(async () => {
        if (orgcontract == null) return;

        const data = await api.org.getFreeAddMember(orgcontract)
        settriggerStatus(data);
        setapplyAuth(data)
        
    }, [orgcontract]);

    const setall = () => {
        setModeratorFunc()
        setMemberFunc()

    }
    const setModeratorFunc = async () => {
        setlistStatus(true);
        try {
            const {lst} = await api.org.getDaoModeratorList(orgcontract, 0, 1000)
            setlist(lst)
            setlistStatus(false)
        } catch (error) {
            console.error('getDaoModeratorList failed', error)
        }
        // order: owner moderator
        
    }
    const setMemberFunc = async () => {
        setmemberlistStatus(true);
        try {
            const {lst} = await api.org.getDaoMembersList(orgcontract, 0, 1000)
            setmemberlist(lst)
            setmemberlistStatus(false);
        } catch (error) {
            console.error('getDaoMembersList failed', error)
        }
    }
    const initOrgContract = async () => {
        if (!orgcontract) return
        setall();
    }

    const getApplyList = async () => {
        // await api.org.getApplyList(orgcontract).then(data => {
        //     if (!data) return;
        //     setnum(data.length)
        // });
    }

    useEffect(async () => {
        initOrgContract();
        getApplyList();
    }, [orgcontract, Account, authcontract, erc20contract]);


    const handleClicktoManage = (type) => {
        setManage(type)

    }
    const handleClose = () => {
        setaddshow(false)
    }
    const handleBatch = () => {
        setaddshow(false);
        setbatchshow(true);
    }
    const handleBatchAdd = () => {
        setaddshow(true);
        setbatchshow(false);
    }
    const handleBatchClose = () => {
        setbatchshow(false);
    }
    const handleAdd = (type) => {
        settypeName(type);
        setaddshow(true)
    }
    const handleAuth = async (address) => {
        setauthshow(true);
        // let list = await api.auth.showActions(authcontract);
        // setauthlist(list);
        setaddress(address)
    }
    const handleAuthClose = () => {
        setauthshow(false);
        setaddress('');
        setauthlist([])

    }
    const handleApplist = () => {
        setapplyshow(false)
    }
    const handleApplistShow = () => {
        setapplyshow(true)
    }
    const handleApplyClose = () => {
        setaddapplyshow(false)
    }
    const handleaddApply = () => {
        setaddapplyshow(true)
    }
    const handleAppTips = () => {
        setshowTips(true)
    }
    const handleAppTipsClose = () => {
        setshowTips(false)
    }
    const handleSetApply = async () => {
        dispatch({ type: 'LOADINGTYPE', payload: `${t('org.Setting')}` });
       
        console.log("*** triggerStatus", triggerStatus)
        try {
            const signer = web3Instance.getSigner(Account)
            await api.org.configJoin(daoOrg, signer, !triggerStatus)

            setapplyAuth(!applyAuth);
            settriggerStatus(!triggerStatus);
        } catch (error) {
            console.error("configJoin failed", error)
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
       

        // await api.org.setFreeAddMember(orgcontract, !applyAuth, (data) => {
        //     setapplyAuth(!applyAuth);
        //     settriggerStatus(!triggerStatus);
        //     dispatch({ type: 'LOADINGTYPE', payload: null });
        // }).catch((error) => {
        //     seterrorShow(true);
        //     dispatch({ type: 'MSGTYPE', payload: { msg: `Org: ${error.message}`, closable: true  } })
        //     dispatch({ type: 'LOADINGTYPE', payload: null });
        // });
    }

    const handleClicktoview = async (item, type) => {
        let obj = {
            name: [item.name],
            address: [item.address]
        }
        const signer = web3Instance.getSigner(Account)

        if (type === 'moderators') {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('org.Removing')}` });
            try {
                const res = await api.org.removeDaoModerator(daoOrg, signer, obj)
                await res.wait();
                setModeratorFunc();
            } catch (error) {
                console.error('removeDaoModerator failed: ', error)
                seterrorShow(true)
                dispatch({ type: 'MSGTYPE', payload: { msg: `${error.message}` , closable: true } })
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }
        } else if (type === 'members') {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('org.Removing')}` });
            try {
                const res = await api.org.removeDaoMember(daoOrg, signer, obj)
                await res.wait();
                setMemberFunc();
            } catch (error) {
                console.error('removeDaoMember failed: ', error)
                seterrorShow(true)
                dispatch({ type: 'MSGTYPE', payload: { msg: ` ${error.message}`, closable: true  } })
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }
        }
    }

    const setDaoOwner = useCallback(
        (o) => {o && setowner(o)},
        []
    )

    const updateDaoID = useCallback(
        id=>setId(id),
        []
    )

    return (
        <div>
            {
                !isVentureDao && <>
                    <ApplyList handleClose={handleApplist} showTips={applyshow} refresh={setall} setnumHandle={getApplyList}/>
                    <ApplyTips showTips={showTips} handleClose={handleAppTipsClose} />
                    <Addnew
                        handleClose={handleClose}
                        showTips={addshow}
                        typeName={typeName}
                        refresh={setall}
                        handleBatch={handleBatch}
                        applyAuth={applyAuth}
                    />
                    <AddApply handleClose={handleApplyClose} showTips={addapplyshow} handleTips={handleAppTips} refresh={setall} />
                    <AddBatch
                        handleClose={handleBatchClose}
                        showTips={batchshow}
                        refresh={setall}
                        handleBatch={handleBatch}
                        handleBatchAdd={handleBatchAdd}
                    />
                    <AddAuth handleClose={handleAuthClose} showTips={authshow} authlist={authlist} address={address} />
                </>
            }

            <div className='container'>
                <FirstLine>
                    <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} setDaoOwner={setDaoOwner} />
                    {
                        !isVentureDao && <>
                            <BtnRht>
                            {
                                isOwner && !manage &&
                                <SwitchBrdr>
                                    <div>{t('org.join')}</div>
                                    <SwitchBtn src={triggerStatus ? TriggerBtnActive : TriggerBtn} alt="" onClick={() => handleSetApply()} />
                                </SwitchBrdr>
                            }
                            {
                                isOwner && !manage &&
                                <Badge count={num}>
                                    <Button onClick={handleApplistShow}>{t('org.Applicants')}</Button>
                                </Badge>
                            }
                            {(isOwner || isModerator) && !manage && <Button type="primary" onClick={() => handleClicktoManage(true)}>{t('org.Manage')}</Button>}
                            {
                                (isOwner || isModerator) && manage && <Button className="active" onClick={() => handleClicktoManage(false)}>{t('org.Complete')}</Button>
                            }
                        </BtnRht>
                        </>
                    }
                    
                </FirstLine>

                <section>
                    <div className="titleTop">{isVentureDao ? t('org.Master') : t('org.Moderators')}</div>
                    <UlMdrt>
                        {
                            listStatus && <LoadingNew />
                        }
                        {
                            !listStatus && owner && (
                                <li>
                                    <img src={OwnerImg} alt="" />
                                    <div>
                                        <div className="names">{t('org.Owner')}</div>
                                        <Address>{owner}</Address>
                                    </div>
                                </li>
                            )
                        }
                        {
                            !listStatus && list.map((item, index) => <li key={`moderators_${item.address}`}>
                                <img src={AdminImg} alt="" />
                                <div>
                                    <div className="names">{item.name}</div>
                                    <Address>{item.address}</Address>
                                </div>
                                {
                                    isOwner && !manage && <img src={AuthImg} alt="" onClick={() => handleAuth(item.address)} className="imgAuth" />
                                }
                                {
                                    manage && <img src={Remove} alt="" className="imgRemove" onClick={() => handleClicktoview(item, 'moderators')} />
                                }

                            </li>)
                        }
                        {
                            !isVentureDao && isOwner && <li onClick={() => handleAdd('Moderators')}>
                                <img src={AddP} alt="" />
                                <NamesAdd>{t('org.AddModerator')}</NamesAdd>
                            </li>
                        }
                    </UlMdrt>
                </section>
                <section>
                    <div className="titleTop" style={{marginTop: '1rem'}}>{isVentureDao ? t('org.GP') : t('org.Members')}</div>
                    <UlMdrt>
                        {
                            memberlistStatus && <LoadingNew />
                        }
                        {
                            !memberlistStatus && memberlist.map((item, index) => <li key={`members_${item.address}`}>
                                <img src={AdminImg} alt="" />
                                <div>
                                    <div className="names">{item.name}</div>
                                    <Address>{item.address}</Address>
                                </div>
                                {
                                    isOwner && !manage && <img src={AuthImg} alt="" onClick={() => handleAuth(item.address)} className="imgAuth" />
                                }
                                {
                                    manage && <img src={Remove} alt="" className="imgRemove" onClick={() => handleClicktoview(item, 'members')} />
                                }
                            </li>)
                        }
                        {
                            !isVentureDao && ( isOwner || isModerator) &&  <li onClick={() => handleAdd('Members')}>
                                <img src={AddP} alt="" />
                                <NamesAdd>Add Member</NamesAdd>
                            </li>
                        }
                        {/*{*/}
                        {/*    !applyAuth && <li onClick={() => handleaddApply()}>*/}
                        {/*        <img src={AddP} alt="" />*/}
                        {/*        <NamesAdd>Apply Member</NamesAdd>*/}
                        {/*    </li>*/}
                        {/*}*/}
                    </UlMdrt>
                </section>
            </div>
        </div>
    )
}

