import React, { useCallback, useEffect, useState } from "react"
import { useTranslation } from "react-i18next";
import { Pagination } from "antd"
import { useWeb3 } from "../../api/contracts";
import { getDaoList } from "../../api/apiHTTP"
import api from "../../api";
import { ethers } from "ethers"
import useGateway from "../../hooks/useGateway"
import LogoSVG from '../../img/logoSubdao.svg'
import { ReactSVG } from 'react-svg'
import LoadingNew from "../loadingNEW";
import { DAOS, PageBrdr, DaosNoData } from './Styles'

const MyDaoList = React.memo((props) => {
    const { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { maincontract, Account, ChainID } = state;

    const [loading, setLoading] = useState(true)
    const [myList, setMyList] = useState([])

    const [pageTotal, setPageTotal] = useState(0);
    const [page, setPage] = useState(1);
    const pageSize = 10;

    const gateway = useGateway()

    const getDaos = useCallback(
        async (daoFactoryContract, account, currentPage) => {
            try {
                const res = await getDaoList(account, currentPage - 1, pageSize)
                if (!res) {
                    throw new Error("getDaoList failed")
                }
                if (res.errors) {
                    dispatch({ type: 'MSGTYPE', payload: { msg: res.errors, closable: true } });
                    return
                }
                const ids = res.orgMembers.map(o => ethers.BigNumber.from(o.daoId))
                const datas = await api.dao.getDaosById(daoFactoryContract, ids)
                const list = datas.daos.map((d, i) => {
                    return {
                        name: d.name,
                        logo: gateway && d.logo ? <img src={gateway + d.logo} alt="" /> : (<ReactSVG className='svg' src={LogoSVG} />),
                        desc: d.desc,
                        key: d.id.toString(),
                        id: d.id,
                        handle: datas.aliases[i],
                        owner: d.owner,  // account
                    }
                })
                const total = Number(res.totalCount && res.totalCount.count)
                return { list, total }
            } catch (error) {
                console.error(error)
                dispatch({ type: 'MSGTYPE', payload: { msg: t('mydao.DaosError'), closable: true } });
                return {list:[], total:0}
            }
        },
        [gateway]
    )

    useEffect(async () => {
        if (!maincontract || !page || !ChainID || !Account || !gateway) {
            return;
        }
        if (!loading) {
            setLoading(true)
        }
        const { list, total } = await getDaos(maincontract, Account, page)
        setMyList(list)
        setPageTotal(total)

        setLoading(false)

    }, [page, maincontract, ChainID, Account, gateway])

    return (
        <DAOS>
            {
                loading && <LoadingNew />
            }
            {
                !loading && myList.map((item, i) =>
                    <div key={item.id} className="daoItem" onClick={() => { props.handleClicktoAbout(item.handle || item.id) }}>
                        {item.logo}
                        <div className="title">{item.name}</div>
                        <div className="detail">{item.desc}</div>
                    </div>
                )
            }
            {
                !loading && myList.length === 0 && <DaosNoData text={t('mydao.noDaos')} />
            }
            {
                myList.length !== 0 && <PageBrdr>
                    <Pagination defaultCurrent={page} total={pageTotal} pageSize={pageSize} onChange={p => setPage(p)} />
                </PageBrdr>
            }
        </DAOS>
    )
})

export default MyDaoList