import React, { useEffect, useMemo, useRef, useState } from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";
import { useWeb3 } from "../api/contracts";
import LogoUpload from './upload/logo'
import {useTranslation} from "react-i18next";
import api from "../api"

const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }

    .inputBrdr .ant-input-affix-wrapper {
        padding-top: 0 !important;
        background: #F4F4FA !important;
        height: 7.6rem !important;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`

const TextAreaDiv = styled.div`
    .textarea {
        height: 19.1rem !important;
    }
`


const EditProfileModal = React.memo((props) => {
    const { show , closeModel, data, updateDaoBase } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { maincontract, web3Instance, Account } = state;
    

    const { TextArea } = Input

    const [name, setName] = useState()
    const [daoID, setDaoID] = useState()
    const [logo, setLogo] = useState()
    const [desc, setDesc] = useState()
    const [twitter, setTwitter] = useState()
    const [discord, setDiscord] = useState()

    const logoRef = useRef()

    useEffect(()=>{
        if (props.data.logo) setLogo(props.data.logo.split('/').slice(-1)[0])
        setName(props.data.name)
        setDesc(props.data.desc)
        setDiscord(props.data.discordLink)
        setDaoID(props.data.daoID)
        if (props.data.twitterLink) setTwitter(props.data.twitterLink.split('/').slice(-1)[0])
    }, [props.data])

  
    const handleClose = () => {
        logoRef.current.clear()
        closeModel()
    }
    

    const confirmUpload = async ()=>{
        handleClose()
        dispatch({ type: 'LOADINGTYPE', payload: t('about.Modified') });
        try {
            const signer = web3Instance.getSigner(Account)
            
            const params = {
                logo, name, desc, socialUrls: [twitter, discord].join(';')
            }
            await api.dao.modifiedDaoBase(maincontract, signer, daoID, params)
            updateDaoBase(params)
        } catch (error) {
            console.error("Modified failed: ", error)
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    const submitDisabled = useMemo(
        ()=>{
            return !name || !desc
        },
        [name, desc]
    )

    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span>{t('create.Basicinformation')}</span>
        </div>
        <div className="label">{t('create.DAOImage')}</div>
        <LogoUpload ref={logoRef} imgCode={logo} updateUrl={url=>setLogo(url)}/>

        <div className="label">{t('create.DAOName')}</div>
        <div className="inputBrdr">
            <Input
                placeholder={t('venture.enter')}
                name='name'
                value={name}
                autoComplete="off"
                onChange={e=>{setName(e.target.value)}}
            />
        </div>

        <div className="label">{t('create.twitter')}</div>
        <div className='inputBrdr'>
            <Input
                placeholder={t('create.twitterTips')}
                prefix="@"
                value={twitter}
                name='twitter'
                autoComplete="off"
                onChange={e=>setTwitter(e.target.value)}
            />
        </div>
        
        <div className="label">{t('create.discord')}</div>
        <div className='inputBrdr'>
            <Input
                placeholder={t('create.discordTip')}
                value={discord}
                name='discord'
                autoComplete="off"
                onChange={e=>setDiscord(e.target.value)}
            />
        </div>
       
        <div className="label">{t('create.DAODescription')}</div>
        <TextAreaDiv className="inputBrdr">
            <TextArea
                className="textarea"
                placeholder={t('create.DAODescription')}
                name='desc'
                value={desc}
                onChange={e=>{setDesc(e.target.value)}}
            />
        </TextAreaDiv>

        <NextBrdr >
            <Button type="primary"  style={{ width: '100%', marginTop: '3rem' }} disabled={submitDisabled} onClick={confirmUpload}>
                {t('venture.Submit')}
            </Button>
        </NextBrdr>
    </ModalBox>
})

export default EditProfileModal