import React, { useEffect, useState, useRef } from 'react';
import { useWeb3 } from "../../api/contracts";
import api from "../../api";
import { Trans, Translation, useTranslation } from 'react-i18next';
import { Modal, Button, Input } from 'antd';
import { ethers } from 'ethers'
import styled from 'styled-components';
import right from '../../img/right.png';
import left from "../../img/left.png";
import VoteConfig from './voteConfig';
import { StepContentArea, StepFooter } from './style'
import PreviewModal from './preview/preview'

const Content = styled.div`
    .line{
        margin-top: 3rem;
        .title{
            height: 2.1rem;
            font-size: 1.8rem;
            font-family: HelveticaNeue;
            font-weight: 300;
            color: #10164B;
            line-height: 2.1rem;
            margin-bottom: 1rem;
        }
        .previous{
            color: #e34b8a;
            border:0.15rem solid #e34b8a;
            width: 18rem;
            height: 5rem;
            padding: 0;
        }
        .nextBtn{
            width: 18rem;
            height: 5rem;
            border-radius: 0.8rem;
            font-family: HelveticaNeue;
            padding: 0;
            &:disabled{
                opacity: 0.3;
            }
        }
    }
`

const Tip = styled.div`
    text-align: center;
    padding: 5rem 2rem;
    font-size: 1.8rem;
    font-family: HelveticaNeue;
    font-weight: 300;
    color: #010643;
    line-height: 2.1rem;
`

const Info = styled.div`
    margin-bottom: 6rem;

    .title{
        height: 4rem;
        font-size: 3.4rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        color: #10134E;
        line-height: 4rem;
        margin-bottom: 0.9rem;
    }
    .detail{
        width: 60rem;
        font-size: 1.8rem;
        font-family: HelveticaNeue;
        font-weight: 300;
        color: #A6A6B7;
        line-height: 2.2rem;
    }
`

export default function ThirdStep(props) {

    const { state, dispatch } = useWeb3();
    const { maincontract, daoManagercontract, web3Instance, Account } = state;

    const [admin, setAdmin] = useState(false);
    const [token, setToken] = useState(true);
    const [name, setname] = useState('');
    const [symbol, setsymbol] = useState('');
    const [supply, setsupply] = useState(null);
    const [adminlist, setadminlist] = useState([
        {
            name: '',
            address: ''
        }
    ]);
    const [tokenlist, settokenlist] = useState([
        {
            address: '',
            token: ''
        }
    ]);
    const [start, setstart] = useState(false);

    const [instanceByTemplate, setinstanceByTemplate] = useState(false);

    const [logo, setlogo] = useState('');
    const [daoname, setdaoname] = useState('');
    const [desc, setdesc] = useState('');
    const [daohandle, setDaohandle] = useState('')

    const [next, setnext] = useState(false);
    const [adminstate, setadminstate] = useState(false);
    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');
    const [showDisable, setshowDisable] = useState(false);

    const [tokenAddress, setTokenAddress] = useState('')
    const [componentsList, setComponentsList] = useState(null)
    const [newDaoId, setNewDaoId] = useState(null);
    const [newDaoBase, setNewDaoBase] = useState({})

    const [socialUrls, setSocialUrls] = useState('')


    let { t } = useTranslation();

    const saveStepData = () => {
        sessionStorage.setItem('forthStep', JSON.stringify({
            template: 0,
            data: {
                name: name,
                symbol: symbol,
                supply: supply
            }
        }));
    }

    const voteRef = useRef();
    const preivewRef = useRef()

    const checkInput = () => {
        if (!name || !symbol || !supply || supply < 0) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('create.venture.FillTip'), closable: true } });
            return
        }
        if (voteRef.current.checkInvalid()) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('vote.VoteConfigError'), closable: true } });
            return
        }
        return true
    }

    const clickPreivew = () => {
        if (!checkInput()) return
        preivewRef && preivewRef.current && preivewRef.current.show({
            baseInfo: {
                name: daoname,
                logo: logo,
                desc: desc,
                socialUrls,
            },
            templateID: 0,
            data: {
                tokenName: name,
                tokenSymbol: symbol,
                tokenSupply: supply,
                vote: voteRef.current.getVoteConfig()
            }
        })
    }

    const Submit = () => {
        // forthStep: {template: 0, data: xxx}
        saveStepData()
        setstart(true)
    }

    useEffect(() => {
        toDataFormat()
    }, [])

    const toThirdStep = () => {
        props.handlerSet(2);
        saveStepData()
    }

    const addtoken = () => {

        let newArray = [...tokenlist];
        newArray.push({
            address: '',
            token: ''

        });
        settokenlist(newArray)
    }

    const setAddress = (e, index) => {
        let newArray = [...tokenlist];
        const { name, value } = e.target;
        newArray[index][name] = value;
        settokenlist(newArray)
    }

    const removeToken = (selectItem, index) => {
        let newArray = [...tokenlist];
        newArray.splice(index, 1);
        settokenlist(newArray)

    }

    const handleInput = (e) => {
        const { name, value } = e.target;

        switch (name) {
            case 'tokenAddress':
                setTokenAddress(e.target.value);
                break;
            case 'name':
                setname(e.target.value);
                break;
            case 'symbol':
                setsymbol(e.target.value);
                break;
            case 'supply':
                setsupply(e.target.value);
                break;
            default:
                break;
        }

    }

    const handleCheck = (e) => {
        const { name, value } = e.target;
        switch (name) {
            case 'admin':
                setAdmin(!JSON.parse(value))
                break;
            case 'token':
                setToken(!JSON.parse(value))
                break;
            default:
                break;
        }
    }

    useEffect(() => {
        let form
        try {
            form = JSON.parse(sessionStorage.getItem('forthStep'));
        } catch (error) {

        }
        if (form && form.template === 0) {
            // setAdmin(form.admin);
            // setToken(form.token);
            form.data && setname(form.data.name);
            form.data && setsymbol(form.data.symbol);
            form.data && setsupply(form.data.supply);
            // settokenlist(form.tokenlist);
        }

    }, []);

    useEffect(() => {
        setshowDisable(!!tokenAddress)
    }, [tokenAddress]);

    const toDataFormat = () => {
        const firstStep = JSON.parse(sessionStorage.getItem('firstStep'));
        // const imgUrl = JSON.parse(sessionStorage.getItem('imgUrl'));

        if (firstStep != null) {
            setdaoname(firstStep.name);
            setdesc(firstStep.description);
            setlogo(firstStep.imgUrl);
            setSocialUrls(firstStep.socialUrls || '');
            setDaohandle(firstStep.daohandle || '')
        }

        const secondStep = JSON.parse(sessionStorage.getItem('secondStep'));
        setComponentsList(secondStep)

        // const thirdStep = JSON.parse(sessionStorage.getItem('thirdStep'));
        // const {admin,adminlist} = thirdStep;

        setadminstate(false);
        // if(admin){
        //     setadminlist(adminlist);
        // }

    }


    useEffect(() => {
        if (!start) return;
        const initDao = async () => {
            dispatch({ type: 'LOADINGTYPE', payload: t('create.InstanceByTemplate') });
            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).instanceByTemplate(
                    [daoname, logo, desc],
                    daohandle,
                    socialUrls,
                    "DaoTemplate",
                    ["OrgManager", "GovTokenManager"],
                    [
                        "0x", "0x",
                        ethers.utils.defaultAbiCoder.encode(
                            // string memory name, string memory symbol, uint256 supply, address owner
                            ['string', 'string', 'uint256', 'address'],
                            [name, symbol, ethers.utils.parseEther(String(supply)), Account]
                        )
                    ]
                );
                const r = await res.wait();
                const event = r.events.filter(e => e.event === 'InstanceDAO')[0]

                const newDaoId = event.args[0].toString()
                setNewDaoId(newDaoId)
            } catch (error) {
                console.error(error)
                seterrorShow(true)
                seterrorTips(`${t('create.init')} ${(error.data && error.data.message) || error.message || ''}`)
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setstart(false);
            }
        }
        initDao()

    }, [maincontract, start]);

    useEffect(() => {
        if (!newDaoId) return
        // add VoteManager component
        dispatch({ type: 'LOADINGTYPE', payload: `${t('create.voteTips')}` });
        const getDao = async () => {
            try {
                const data = await maincontract.instanceMap(parseInt(newDaoId));
                setNewDaoBase(data)
            } catch (error) {
                console.error('instanceMap failed', error)
            }
        }
        getDao()
    }, [newDaoId])

    useEffect(() => {
        if (Object.keys(newDaoBase).length === 0) return
        const getVoteManagerParams = () => {
            const { minNumber, minSupportNumber } = voteRef.current.getVoteConfig()
            return ethers.utils.defaultAbiCoder.encode(
                ['uint256', 'uint256'],
                [minNumber, minSupportNumber]
            )
        }

        const getVaultParams = (address) => {
            return ethers.utils.defaultAbiCoder.encode(
                ['address'],
                [address]
            )
        }

        const getGrantMethodManagerParams = () => {
            return "0x"
        }

        const addComponents = async () => {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('create.manager')}` });

            let token;
            try {
                const daoOrg = await api.daoOrg.getContract(web3Instance, newDaoBase.daoOrganization)
                const templateConfigAddress = await daoOrg.templateConfig()
                const templateConfig = await api.template.getContract(web3Instance, templateConfigAddress)
                token = await templateConfig.labelComponent('GovTokenManager')
            } catch (error) {
                console.error('get VenturesStockManager address failed', error)
            }
            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).addComponents(newDaoId, [
                    'VaultManager',
                    'VoteManager',
                    'GrantMethodManager',
                ], [
                    getVaultParams(token),
                    getVoteManagerParams(),
                    getGrantMethodManagerParams()
                ])
                await res.wait()
                setnext(true);
            } catch (error) {
                console.error(error)
                seterrorShow(true)
                seterrorTips(`addComponents ${error.data.message}`)
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }
        }
        addComponents()

    }, [newDaoBase])

    useEffect(() => {
        if (!next) return
        sessionStorage.removeItem("step");
        sessionStorage.removeItem("secondStep");
        sessionStorage.removeItem("firstStep");
        sessionStorage.removeItem("ImageUrl");
        sessionStorage.removeItem("forthStep");
        dispatch({ type: 'LOADINGTYPE', payload: null });
        props.history.push(`/about/${daohandle}`);
        dispatch({ type: 'RELOAD_DAOS', payload: true });
    }, [next])

    return <Content className="content">

        <Modal
            visible={errorShow}
            onCancel={() => seterrorShow(false)}
            footer={null}
        >
            <Tip>{errorTips}</Tip>
        </Modal>
        <StepContentArea>
            <Info>
                <div className="title">{t('create.token.config')}</div>
                {/*<div className='detail'>Token Configuration</div>*/}
            </Info>

            <Translation>{t =>
                <div title={t('Token')} >
                    <div className="line">
                        <div className="title">{t('create.token.name')}</div>
                        <div className='inputBrdr input-line'>
                            <Input
                                placeholder={t('create.token.FillToken')}
                                value={name}
                                checked={name}
                                name='name'
                                autoComplete="off"
                                onChange={handleInput}
                            /></div>
                    </div>
                    <div className="line">
                        <div className="title">{t('create.token.Symbol')}</div>
                        <div className='inputBrdr input-line'>
                            <Input
                                placeholder={t('create.token.FillSymbol')}
                                value={symbol}
                                checked={symbol}
                                name='symbol'
                                autoComplete="off"
                                onChange={handleInput}
                            />
                        </div>
                    </div>
                    <div className="line">
                        <div className="title">{t('create.token.Total')}</div>
                        <div className='inputBrdr input-line'>
                            <Input
                                placeholder={t('create.token.FillSupply')}
                                value={supply}
                                checked={supply}
                                type='number'
                                name='supply'
                                autoComplete="off"
                                onChange={handleInput}
                            />
                        </div>
                    </div>
                    <VoteConfig ref={voteRef}></VoteConfig>
                </div>
            }
            </Translation>
        </StepContentArea>
        <StepFooter>
            <Button className='previous' style={{ marginRight: '3rem' }} onClick={() => toThirdStep()}>
                <img className="left" src={left} alt="" />
                {t('create.think')}
            </Button>
            <Button type="primary" onClick={() => clickPreivew()} className="nextBtn">
                {/* <Button type="primary" onClick={()=>Submit()} className="nextBtn"  disabled={!showDisable}> */}
                {t('create.preview')}
                <img src={right} alt="" />
            </Button>
        </StepFooter>
        <PreviewModal ref={preivewRef} doSubmit={Submit} />
    </Content>;

}
