import React, { useState, useEffect } from 'react';
import StepNav from './components/stepNav';
import MySteps from './components/MySteps';
import FirstStep from './components/create/firstStep';
import SecondStep from './components/create/secondStep';
import ThirdStep from './components/create/thirdStep';
import ForthStep from './components/create/forthStep';
import VentureConfig from './components/create/ventureConfig'
import styled from 'styled-components';


const Container = styled.div`
    background: #fff;
    padding-top: 6rem;
    .createSingle{
        width: 110rem;
        margin: 0 auto;
    }
`


export default function Createnew(props) {

    const [type, settype] = useState(1);
    const [template, setTemplate] = useState(0)

    const setStep = (i) => {
        settype(i);
        sessionStorage.setItem('step', i)
    }
    const handleTemplate = (templateId) => {
        setTemplate(templateId)
    }
    return (
            <Container>
                <div className="createSingle">
                     <StepNav type={type} />
                    {/*<MySteps type={type} />*/}

                    <div className='createBrdr'>
                        {type === 1 && <FirstStep handlerSet={setStep} />}
                        {type === 2 && <SecondStep handlerSet={setStep} history={props.history} handleTemplate={handleTemplate} />}
                        {type === 3 && (
                           (template === 0 &&  <ForthStep handlerSet={setStep} history={props.history} />) || (template === 1 &&  <VentureConfig handlerSet={setStep} history={props.history} />)
                        )}
                        {/* {type === 4 && <ForthStep handlerSet={setStep}  history={props.history}/>} */}
                    </div>
                </div>
            </Container>
    );

}
