import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import styled from 'styled-components';
import { useLocation } from 'react-router-dom';
import * as history from 'history';
import Medium from '../img/foot/Medium.png';
import Github from '../img/foot/Github.png';
import Twitter from '../img/foot/Twitter.png'
import Telegram from '../img/foot/Telegram.png'

const createHashHistory = history.createHashHistory();

const Footer = styled.div`
    width: 100%;
    height: 6rem;
    background: #10164B;
    display: flex;
    align-items: center;

    .row{
        width: 144rem;
        margin: 0 auto !important;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            height: 4rem;
            line-height: 4rem;
            font-size: 1.8rem;
            opacity: 0.7;
            color: #FFFFFF;
        }
        .rht{
            text-align: right;
            display: flex;
            font-size: 1.8rem;
            a{
                margin-left: 4.8rem;
                color: #10164B;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 1.8rem;
                font-weight: 300;
                font-family: HelveticaNeue;
                opacity: 0.7;
                img{
                    width: 4rem;
                    height: 4rem;
                    transition: all 200ms linear;
                    &:hover{
                        transform: scale(1.2);
                        transition: all 300ms linear;
                    }
                }
            }
        }
    }
`

const FootBtm = (props) => {
    const { pathname } = useLocation()
    const [show, setshow] = useState(false)

    useEffect(() => {
        if (pathname !== '/home') {
            setshow(false)
        } else {
            setshow(true)
        }
    }, [pathname])

    const toFirst = () => {
        createHashHistory.push('/')
    }

    let { t } = useTranslation();
    return <section>
        <Footer>
            {/*<div className="row" style={{ justifyContent: show ? 'space-between' : 'center' }}>*/}
            <div className="row" style={{ justifyContent:'space-between' }}>
                <div className="left">{t('footer.right')}</div>
                {!show && <div className="rht" onClick={() => toFirst()}>{t('footer.Home')}</div>}
                {show && <div className='rht'>

                    {/* <a href="https://www.subdao.network/" rel="noopener noreferrer" target="_blank">
                        <img src={subdaoicon} alt="" />
                        {t('Website')}
                    </a> */}
                    <a href="https://medium.com/@subdao" target="_blank" rel="noopener noreferrer">
                        <img src={Medium} alt="" />
                        {/* {t('footer.Medium')} */}
                    </a>
                    <a href="https://github.com/SubDAO-Network" target="_blank" rel="noopener noreferrer">
                        <img src={Github} alt="" />
                        {/* {t('footer.Github')} */}
                    </a>
                    {/* <a href="https://drive.google.com/file/d/1L76e16DtK16Edjex7m_PRcTdaEzrLxBs/view" rel="noopener noreferrer" target="_blank">
                        <img src={LightPaper} alt="" />
                        Light Paper
                    </a> */}
                    <a href="https://twitter.com/subdao_network" target="_blank" rel="noopener noreferrer">
                        <img src={Twitter} alt="" />
                         {/* {t('footer.Twitter')} */}
                    </a>
                    <a href="https://t.me/subdao" target="_blank" rel="noopener noreferrer">
                        <img src={Telegram} alt="" />
                        {/* {t('footer.Telegram')} */}
                    </a>
                </div>}
            </div>
        </Footer>
    </section>;
};

export default FootBtm;
