import React, {useEffect, useState} from 'react';
import { useWeb3 } from "../../api/contracts";
import VoteView from '../vote/voteView';
import { Empty } from 'antd'
import VotingImg from '../../img/votingImg.png';

import PublicJS from '../../utils/publicJs';
import styled from 'styled-components';
import api from '../../api';
import {useTranslation} from "react-i18next";

const BtnSpan = styled.span`
    padding:0 0.5rem;
    background: #EEF5F0;
    border-radius: 0.4rem;
    border: 0.1rem solid #6AB861;
    display: inline-block;
    text-align: center;
    line-height: 2.6rem;
    font-size: 1.2rem;
    font-family: HelveticaNeue;
    font-weight: 400;
    color: #6AB861;
    img{
    display: inline-block;
    width: 1.5rem;
    margin-right: 0.5rem;
    }
`


export default function VoteActive(props){
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { Account, votecontract } = state;

    const [indexList, setIndexList] = useState([]);
    const [newshow, setnewshow] = useState(false);
    const [voteid, setvoteid] = useState(null);

    useEffect(() => {
        if(props.list.length){
            setIndexList(props.list)
        }
    }, [props]);

    const  handleClose = () => {
        setnewshow(false)
    }

    const handleView = async (voteid) => {
        // if has vote

        setnewshow(true)
        setvoteid(voteid)
    }

    const handleClicktoVoteview = (voteid) => {
        let { handle, owner } = props;
        props.history.push(`/voteOverview/${handle}/${voteid}`)

    }
        return (
            <div className='votePending'>
                {
                    newshow &&   <VoteView  handleClose={handleClose} showTips={newshow} voteid={voteid} id={props.id}/>
                }

                <table className="myTable votePending">
                    <thead>
                    <tr>
                        <th>{t('vote.Number')}</th>
                        <th>{t('vote.Time')}</th>
                        <th>{t('vote.Title')}</th>
                        <th>{t('vote.Action')}</th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        indexList.map((item, index)=><tr key={`active_${item.vote_id}`}>
                            <td onClick={()=>handleClicktoVoteview(item.vote_id)}>{index + 1}</td>
                            <td onClick={()=>handleClicktoVoteview(item.vote_id)}>{item.endDisplay}</td>
                            <td onClick={()=>handleClicktoVoteview(item.vote_id)}>{item.title}</td>
                            <td>
                                <BtnSpan onClick={()=>handleView(item.vote_id)}><img src={VotingImg} alt=""/>{t('vote.Vote')}</BtnSpan>
                            </td>

                        </tr>)
                    }
                    </tbody>
                </table>
                {
                    indexList.length === 0 && <Empty style={{padding: '1.2rem', margin: 0, background: '#fff'}} image={Empty.PRESENTED_IMAGE_SIMPLE} />
                }
            </div>
        )


}
