import React, { useEffect, useState } from 'react';

import VoteModalTips from "./votemodalTips";
import api from "../../api";
import { useWeb3 } from "../../api/contracts";
import VotingImg from '../../img/votingImg.png';
import styled from 'styled-components';
import {useTranslation} from "react-i18next";
import { Empty } from 'antd'

const BtnSpan = styled.span`
    padding:0 0.5rem;
    background: #EEF5F0;
    border-radius: 0.4rem;
    border: 0.1rem solid #6AB861;
    display: inline-block;
    text-align: center;
    line-height: 2.6rem;
    font-size: 1.2rem;
    font-family: HelveticaNeue;
    font-weight: 400;
    color: #6AB861;
    img{
        display: inline-block;
        width: 1.5rem;
        margin-right: 0.5rem;
    }
`


export default function VotePending(props) {
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, votecontract, daoOrg } = state;

    const [showModal, setShowModal] = useState(false);
    const [list, setList] = useState([]);

    const [selectVote, setSelectVote] = useState()

    useEffect(() => {
        setList(props.list)
    }, [props.list]);

    const triggerConfirm = async (item) => {
        setSelectVote(item)
        setShowModal(true)
    }
    const handleClose = () => {
        setShowModal(false);
        setSelectVote(null)
    }
    const handleConfirm = async (e) => {
        setShowModal(false);
        let { handle, owner } = props;

        dispatch({ type: 'LOADINGTYPE', payload: t('vote.ExecutingVote') });

        // const canExcute = await votecontract.canExecute(selectid)
        // console.log('** canExcute **', canExcute)

        const signer = web3Instance.getSigner(Account)
        try {
            const voteId = selectVote.vote_id
            if (selectVote.isTransfer) {
                await api.vote.executeWithdrawVote(daoOrg, signer, selectVote.wid)
            } else if (selectVote.isApprove) {
                await api.vote.executeApproveGPVote(daoOrg, signer, voteId)
            } else if (selectVote.isKick) {
                await api.vote.executeKickGPVote(daoOrg, signer, voteId)
            } else if (selectVote.isPeriod) {
                await api.vote.executeTransferPeriodVote(daoOrg, signer, voteId)
            } else if (selectVote.isNewGp) {
                await api.vote.executeAddGPVote(daoOrg, signer, voteId)
            } else if (selectVote.isTransferManageFee) {
                await api.vote.executeTransferManageFeeVote(daoOrg, signer, selectVote.wid)
            } else {
                await api.vote.executeVote(daoOrg, signer, voteId)
            }
          
            dispatch({ type: 'BALANCE', payload: true });
            props.history.push(`/voteOverview/${handle}/${voteId}`)
        } catch (error) {
            console.error(error)
            dispatch({ type: 'MSGTYPE', payload: { msg: t('vote.ExecuteFailed', {error: (error.data && error.data.message) || 'unknown'}) } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }
    const handleClicktoVoteview = (voteid) => {
        let { id, owner } = props;
        props.history.push(`/voteOverview/${id}/${voteid}`)
    }
    return (<div className='votePending'>
        <VoteModalTips
            handleClose={handleClose}
            handleConfirm={handleConfirm}
            showTips={showModal} />
        <table className="myTable votePending">
            <thead>
                <tr>
                    <th>{t('vote.Number')}</th>
                    <th>{t('vote.Time')}</th>
                    <th>{t('vote.Title')}</th>
                    <th>{t('vote.Action')}</th>
                </tr>
            </thead>
            <tbody>
                {
                    list.map((item, index) => <tr key={`Pending_${item.vote_id}`}>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{index + 1}</td>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{item.endDisplay}</td>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{item.title}</td>
                        <td>
                            <BtnSpan  onClick={() => triggerConfirm(item)}><img src={VotingImg} alt=""/>{t('vote.Proceed')}</BtnSpan>
                        </td>
                    </tr>)
                }

            </tbody>
        </table>
        {
            list.length === 0 && <Empty style={{padding: '1.2rem', margin: 0, background: '#fff'}} image={Empty.PRESENTED_IMAGE_SIMPLE} />
        }
    </div>)
}

