import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import { Button, Modal, Input, Select } from 'antd';

import { useWeb3 } from "../api/contracts";

import { useTranslation } from "react-i18next";

import styled from "styled-components";

import apiInterface from '../api';

const TopDiv = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 1.6rem;
  background: #F4F4FA;
  width: 100%;
  box-shadow: 0 0 0.4rem 0 rgba(16, 22, 75, 0.1);
  border-radius: 0.8rem!important;
  height: 7.6rem;
  border: 1px solid #d9d9d9;
  line-height: 7.6rem;
  padding: 0 11px;
`;

const HeaderTransfer = forwardRef((props, ref) => {
    const { state, dispatch } = useWeb3();
    const { vaultcontract,erc20contract,allAccounts,api} = state;

    const [selected, setSelected] = useState(null);
    const [address, setaddress] = useState('');
    const [amount, setamount] = useState('');

    const [list, setList] = useState([]);

    let { t } = useTranslation();


    const handleChange = (e) => {
        const { name, value } = e.target;
        let str = `set${name}`
        eval(str)(value)
    }
    const handleConfirm = async () => {


        dispatch({ type: 'LOADINGTYPE', payload: 'Transferring' });
        props.handleClose();
        await apiInterface.main.transferHeader(api,address, amount* Math.pow(10,12),(result)=>{
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }).catch((error)=>{
            dispatch({ type: 'MSGTYPE', payload: { msg: `Transfer: ${error.message}` } });
            dispatch({ type: 'LOADINGTYPE', payload: null });
        });
    }
    const handleSelect = (value) => {
        setSelected(value)
    }
    useImperativeHandle(ref, () => ({
        resultToVault: (newVal) => {
            return {
                address,
                amount: amount * Math.pow(10,12)
            }
        },
        amountToNull: () => {
            setamount('')
            setaddress('')
        }
    }));

    let { handleClose, showTips } = props;
    return (
        <div>
            <Modal visible={showTips} onCancel={handleClose} footer={null}>
                <div className="title">
                    {/* <img src={sender} alt="" /> */}
                    <span>Transfer</span>
                </div>

                {
                    props.balance &&<TopDiv>
                    <span>{props.symbol}</span>
                    <span>
                        Available: {props.balance}
                    </span>
                    </TopDiv>
                }


                <div className="label">Address</div>
                <Input
                    placeholder='Address'
                    value={address}
                    name='address'
                    onChange={handleChange}
                />

                <div className="label">Amount</div>
                <Input
                    placeholder='Amount'
                    value={amount}
                    name='amount'
                    onChange={handleChange}
                />

                <Button type="primary" onClick={() => handleConfirm()} style={{ width: '100%', marginTop: '3.9rem' }}>Submit</Button>
            </Modal>
        </div>
    )

})
export default HeaderTransfer;
