import React, { useEffect, useRef, useState } from "react";
import {Button, Input, Modal} from "antd";
import styled from "styled-components";
import { useWeb3 } from "../../api/contracts";
import Logo from "../upload/img"
import { uploadProject, requestSign } from "../../api/apiHTTP"
import {useTranslation} from "react-i18next";


const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`

const TextAreaDiv = styled.div`
    .textarea {
        height: 19.1rem !important;
    }
`


export default function AddProject(props) {
    const { show , closeModel } = props;
    let { t } = useTranslation();
    const { state, dispatch } = useWeb3();
    const { Account, venturecontract } = state;

    const { TextArea } = Input

    const [name, setName] = useState(null)
    const [homepage, setHomepage] = useState('')
    const [amount, setAmount] = useState(null)
    const [desc, setDesc] = useState(null)

    const imgRef = useRef()


    const handleInputChange = (e) =>{
        const {name, value} = e.target
        switch (name) {
            case 'name':
                setName(value)
                break
            case 'homepage':
                setHomepage(value)
                break
            case 'amount':
                setAmount(Number(value))
                break
            case 'desc':
                setDesc(value)
                break
            default:
                return
        }
    }

    const handleClose = () => {
        imgRef.current.clear()
        closeModel()
    }

    

    const confirmUpload = async ()=>{
        handleClose()
        // upload img
        let imgCode
        imgCode = await imgRef.current.upload()
        if (!imgCode) return
        
        // sign
        const data = {
            daoAddress: venturecontract.address,
            name, desc, logo: imgCode, amount, homepage,
            timestamp: Date.now()
        }
        let signer
        try {
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.signing')}` });
            signer = await requestSign(data, Account)
        } catch (error) {
            console.error(`${t('venture.signFailed')}: `, error)
            return
        }
        // upload project
        try {
            data.signer = signer
            await uploadProject(data)
            console.log(Date.now() - data.timestamp)
            props.updateProject && props.updateProject()
        } catch (error) {
            console.error("Upload Project failed: ", error)
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
        
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span>{t('venture.Upload')}</span>
        </div>
        <div className="label">{t('venture.Project')}</div>
        <Logo ref={imgRef} />

        <div className="label">{t('venture.name')}</div>
        <div className="inputBrdr">
            <Input
                placeholder={t('venture.enter')}
                name='name'
                value={name}
                autoComplete="off"
                onChange={(e)=>handleInputChange(e)}
            />
        </div>
            
        <div className="label">{t('venture.homepage')}</div>
        <div className="inputBrdr">
            <Input
                placeholder={t('venture.enter')}
                name='homepage'
                value={homepage}
                autoComplete="off"
                onChange={(e)=>handleInputChange(e)}
            />
        </div>

        <div className="label">{t('venture.amounts')}</div>
        <div className="inputBrdr">
            <Input
                placeholder={t('venture.enter')}
                name='amount'
                value={amount}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{props.symbol}</span>
        </div>
        <div className="label">{t('venture.ProjectDescription')}</div>
        <TextAreaDiv className="inputBrdr">
            <TextArea
                className="textarea"
                placeholder={t('venture.descriptionEnter')}
                name='desc'
                value={desc}
                onChange={handleInputChange}
            />
        </TextAreaDiv>

        <NextBrdr >
            <Button type="primary"  style={{ width: '100%', marginTop: '3rem' }} onClick={confirmUpload}>
                {t('venture.Submit')}
            </Button>
        </NextBrdr>
    </ModalBox>
}
